import request from '../HttpRequest';
import sendToken from '../HttpRequest/send-token';
import timeStampFormatter from '../../helpers/Formatter/timestamp';

import { pick } from 'lodash';

const pickClusterData = [
  'clusterNumber',
  'location',
  'name',
  'registeredAt',
  'registeredById',
  'sizeOfLand',
  'id',
  'userId'
];

function RegistrationClass() {
  this.getClusters = function(data) {
    sendToken(data);
    return request.get('v1/clusters');
  };
  this.postCluster = function(data) {
    sendToken(data);
    data.registeredAt = timeStampFormatter(data.registeredAt);
    return request.post('v1/clusters', data);
  };
  this.getCluster = function(data) {
    sendToken(data);
    return request
      .get('v1/clusters/' + data.id)
      .then(r => pick(r.data, pickClusterData));
  };
  this.putCluster = function(data) {
    sendToken(data);
    data.registeredAt = timeStampFormatter(data.registeredAt);
    return request.put('v1/clusters/' + data.id, data);
  };
  this.getFarms = function(data) {
    sendToken(data);
    return request.get('v1/farms');
  };
  this.postFarm = function(data) {
    sendToken(data);
    data.registeredAt = timeStampFormatter(data.registeredAt);
    return request.post('v1/farms', data);
  };
  this.getFarm = function(data) {
    sendToken(data);
    return request.get('v1/farms/' + data.id);
  };
  this.putFarm = function(data) {
    sendToken(data);
    data.registeredAt = timeStampFormatter(data.registeredAt);
    return request.put('v1/farms/' + data.id, data);
  };
  this.getFields = function(data) {
    sendToken(data);
    return request.get('v1/farm-fields');
  };
  this.postField = function(data) {
    sendToken(data);
    data.registeredAt = timeStampFormatter(data.registeredAt);
    return request.post('v1/farm-fields', data);
  };
  this.getField = function(data) {
    sendToken(data);
    return request.get('v1/farm-fields/' + data.id);
  };
  this.putField = function(data) {
    sendToken(data);
    data.registeredAt = timeStampFormatter(data.registeredAt);
    return request.put('v1/farm-fields/' + data.id, data);
  };
  this.deleteItem = function(data) {
    sendToken(data);
    return request.delete('v1/' + data.entity, data.id);
  };
}

export default RegistrationClass;



