import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/components/Login';
import ContentContainer from '@/components/ContentContainer';
import Calendar from '@/components/Calendar';
import Clusters from '@/components/territory/Clusters';
import Territory from '@/components/territory/index';
import ClustersForm from '@/components/territory/forms/ClustersForm';
import Farms from '@/components/territory/Farms';
import FarmsForm from '@/components/territory/forms/FarmsForm';
import Fields from '@/components/territory/Fields';
import FieldsForm from '@/components/territory/forms/FieldsForm';
import Reports from '@/components/reports/Reports';
import ReportsForm from '@/components/reports/forms/ReportsForm';
import Seasons from '@/components/seasons/Types';
import Schedulers from '@/components/seasons/Schedulers';
import Activity from '@/components/activity/Activities';
import ActivitySchedulers from '@/components/activity/Schedulers';
import ActivityTypes from '@/components/activity/Types';
import ActivitySchedulersForm from '@/components/activity/forms/Schedulers';
import Users from '@/components/users/Users';
import Roles from '@/components/users/Roles';
import SchedulersForm from '@/components/seasons/forms/SchedulersForm';
import TypesForm from '@/components/seasons/forms/TypesForm';
import ActivityForm from '@/components/activity/forms/ActivityForm';
import ActivityTypesForm from '@/components/activity/forms/ActivityTypesForm';
import UsersForm from '@/components/users/forms/UsersForm';
import RolesForm from '@/components/users/forms/RolesForm';

Vue.use(Router);

const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/dashboard',
      redirect: '/dashboard/calendar'
    },
    {
      path: '/dashboard',
      component: ContentContainer,
      children: [
        {
          path: 'territory',
          name: 'territory',
          redirect: '/dashboard/territory/clusters'
        },
        {
          path: 'calendar',
          name: 'calendar',
          component: Calendar
        },
        {
          path: 'territory/clusters',
          name: 'clusters',
          component: Clusters
        },
        {
          path: 'territory/clusters/:url',
          name: 'ClustersForm',
          component: ClustersForm
        },
        {
          path: 'territory/cluster/:url/:id',
          name: 'ClusterItem',
          component: ClustersForm
        },
        {
          path: 'territory/farms',
          name: 'farms',
          component: Farms
        },
        {
          path: 'territory/farms/:url',
          name: 'FarmsForm',
          component: FarmsForm
        },
        {
          path: 'territory/farms/:url/:id',
          name: 'FarmsItem',
          component: FarmsForm
        },
        {
          path: 'territory/fields',
          name: 'farm-fields',
          component: Fields
        },
        {
          path: 'territory/fields/:url',
          name: 'FieldsForm',
          component: FieldsForm
        },
        {
          path: 'territory/fields/:url/:id',
          name: 'FieldsItem',
          component: FieldsForm
        },
        {
          path: 'reports',
          name: 'reports',
          component: Reports
        },
        {
          path: 'reports/:url',
          name: 'ReportsForm',
          component: ReportsForm
        },
        {
          path: 'reports/:url/:id',
          name: 'ReportsItem',
          component: ReportsForm
        },
        {
          path: 'seasons/list',
          name: 'Seasons',
          component: Seasons
        },
        {
          path: 'seasons/schedulers',
          name: 'Schedulers',
          component: Schedulers
        },
        {
          path: 'seasons/schedulers/:url',
          name: 'SchedulersForm',
          component: SchedulersForm
        },
        {
          path: 'seasons/schedulers/:url/:id',
          name: 'SchedulersItem',
          component: SchedulersForm
        },
        {
          path: 'seasons/types/:url',
          name: 'TypesForm',
          component: TypesForm
        },
        {
          path: 'seasons/types/:url/:id',
          name: 'TypesItem',
          component: TypesForm
        },
        {
          path: 'activity/list',
          name: 'activity',
          component: Activity
        },
        {
          path: 'activity/schedulers',
          name: 'ActivitySchedulers',
          component: ActivitySchedulers
        },
        {
          path: 'activity/categories',
          name: 'ActivityTypes',
          component: ActivityTypes
        },
        {
          path: 'activity/:url',
          name: 'ActivityForm',
          component: ActivityForm
        },
        {
          path: 'activity/:url/:id',
          name: 'ActivityItem',
          component: ActivityForm
        },
        {
          path: 'activity/schedulers/:url',
          name: 'ActivitySchedulersForm',
          component: ActivitySchedulersForm
        },
        {
          path: 'activity/schedulers/:url/:id',
          name: 'ActivitySchedulersItem',
          component: ActivitySchedulersForm
        },
        {
          path: 'activity/types/:url',
          name: 'ActivityTypesForm',
          component: ActivityTypesForm
        },
        {
          path: 'activity/types/:url/:id',
          name: 'ActivityTypesItem',
          component: ActivityTypesForm
        },
        {
          path: 'users/list',
          name: 'Users',
          component: Users
        },
        {
          path: 'users/:url',
          name: 'UsersForm',
          component: UsersForm
        },
        {
          path: 'users/:url/:id',
          name: 'UsersItem',
          component: UsersForm
        },
        {
          path: 'users/roles',
          name: 'Roles',
          component: Roles
        },
        {
          path: 'roles/:url',
          name: 'RolesForm',
          component: RolesForm
        },
        {
          path: 'roles/:url/:id',
          name: 'RolesItem',
          component: RolesForm
        }
      ]
    }
  ]
});

export default router;



