import request from '../HttpRequest';
import sendToken from '../HttpRequest/send-token';

function ReportsClass() {
  this.getReports = function(data) {
    sendToken(data);
    return request.get('v1/reports');
  };
  this.getReport = function(data) {
    sendToken(data);
    return request.get('v1/reports/' + data.id);
  };
  this.putReport = function(data) {
    sendToken(data);
    return request.put('v1/reports/' + data.id, data);
  };
  this.postReport = function(data) {
    sendToken(data);
    return request.post('v1/reports', data);
  };
}

export default ReportsClass;



