<template>
  <div class="form-group">
    <label :for="schema.id">{{schema.fieldLabel}}</label>
    <div class="toggle-switch">
      <input type="checkbox" v-model="value" :autocomplete="schema.autocomplete" :disabled="disabled"  :id="schema.id"/>
      <label :for="schema.id"></label>
    </div>
  </div>
</template>

<script>
  import { abstractField } from 'vue-form-generator';

  export default {
    mixins: [abstractField],

    data: function() {
      return {
        formatValueToModel: ''
      };
    }
  };
</script>

<style>
  .vue-form-generator .field-checkbox input {
    margin-left: 12px;
  }
  .field-switches > label {
    display: none;
  }
</style>



