<template>
  <div>
    <div class='google-address-field'>
      <input type='text' id="autocomplete" v-model='value' :autocomplete='schema.autocomplete' :disabled='disabled' :placeholder='schema.placeholder' :readonly='schema.readonly' :name='schema.inputName' debounce='500' class='form-control'/>
    </div>
    <div class='gmap'>
      <button v-if="removeButton" @click="removeAllMarkers" class="btn btn-sm btn-warning">Remove marker</button>
      <div class='gmap-js' id='gmap'></div>
    </div>
  </div>
</template>

<script>
  import { abstractField } from 'vue-form-generator';
  import GoogleMapsLoader from '../../helpers/GoogleMaps';
  import GMaps from 'gmaps';

  export default {
    mixins: [abstractField],

    data: function() {
      return {
        formatValueToModel: '',
        autocomplete: '',
        inputs: {
          street_number: 'long_name',
          route: 'long_name',
          country: 'long_name',
          administrative_area_level_1: 'long_name',
          administrative_area_level_2: 'long_name',
          locality: 'long_name',
          postal_code: 'short_name'
        },
        map: undefined,
        google: undefined,
        gmapSettings: {
          div: '#gmap',
          zoom: 10,
          lat: '-0.702042',
          lng: '86.472043',
          scrollwheel: false,
          panControl: false,
          mapTypeControl: false,
          overviewMapControl: false,
          streetViewControl: false
        },
        removeButton: false
      };
    },
    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      }
    },
    mounted() {
      GoogleMapsLoader.load(google => {
        this.google = google;
        this.map = new GMaps(this.gmapSettings);
        this.$nextTick(function() {
          if (
            window.google &&
            window.google.maps &&
            window.google.maps.places &&
            window.google.maps.places.Autocomplete
          ) {
            this.autocomplete = new google.maps.places
              .Autocomplete(document.getElementById('autocomplete'), {
              types: ['geocode']
            });
            this.autocomplete.addListener('place_changed', this.pipeAddress);
          } else {
            console.warn(
              'Google Maps API is missing. Please add https://maps.googleapis.com/maps/api/js?key=YOUR_KEY&libraries=places script in the HTML head section!'
            );
          }
        });
        if (!this.pageType) {
          setTimeout(() => {
            this.removeButton = true;
            this.map.addMarker({
              lat: this.model.location.latitude,
              lng: this.model.location.longitude
            });
            this.map.setCenter({
              lat: this.model.location.latitude,
              lng: this.model.location.longitude
            });
          }, 1000);
        }
      });
    },
    methods: {
      pipeAddress: function() {
        let place = this.autocomplete.getPlace();
        if (place) {
          this.removeButton = true;
          this.value = place.formatted_address;
          let data = {};
          if (place.address_components !== undefined) {
            for (let i = 0; i < place.address_components.length; i++) {
              let input = place.address_components[i].types[0];
              if (this.inputs[input]) {
                data[input] = place.address_components[i][this.inputs[input]];
              }
            }
          }
          this.map.removeMarkers();
          this.map.setZoom(14);
          this.model.location.latitude = place.geometry.location.lat();
          this.model.location.longitude = place.geometry.location.lng();
          this.map.addMarker({
            lat: place.geometry.location.lat(),
            lng: place.geometry.location.lng()
          });
          this.map.setCenter({
            lat: place.geometry.location.lat(),
            lng: place.geometry.location.lng()
          });
        }
      },
      removeAllMarkers() {
        this.removeButton = false;
        this.map.removeMarkers();
        this.map.setZoom(5);
        this.map.setCenter({
          lat: 0,
          lng: 0
        });
        this.model.location.nearestCity = '';
      }
    }
  };
</script>

<style>
  .vue-form-generator .form-group.field-google-map {
    display: block;
  }

  .vue-form-generator .form-group.field-google-map label {
    padding-bottom: 5px;
  }

  .vue-form-generator .form-group.field-google-map .field-wrap {
    width: 80%;
  }

  .vue-form-generator .form-group.field-google-map label {
    float: left;
    width: 33%;
  }

  .google-address-field {
    width: 50%;
    display: flex;
    margin-bottom: 40px;
  }

  .gmap {
    position: relative;
    max-width: 90%;
    height: 350px;
    margin-bottom: 1rem;
  }

  .gmap-js, .gmap-image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .gmap .btn {
    position: absolute;
    top: 5px;
    right: 0;
    z-index: 100;
  }
</style>



