import request from '../HttpRequest';
import store from '../../store/index';
import sendToken from '../HttpRequest/send-token';
import { pick } from 'lodash';

const pickUser = [
  'id',
  'email',
  'firstName',
  'lastName',
  'loginName',
  'password',
  'roleIds',
  'token'
];

function UserClass() {
  this.login = function(data) {
    return request.post('auth/login', data).then(r => {
      store.dispatch('setToken', r.token);
      return this.getCurrentUser(r);
    });
  };

  this.getCurrentUser = function(data) {
    sendToken(data);
    return request.get('v1/users/current');
  };

  this.getUsers = function(data) {
    sendToken(data);
    return request.get('v1/users');
  };

  this.getUser = function(data) {
    sendToken(data);
    return request.get('v1/users/' + data.id);
  };

  this.getRoles = function(data) {
    sendToken(data);
    return request.get('v1/roles');
  };

  this.getRole = function(data) {
    sendToken(data);
    return request.get('v1/roles/' + data.id);
  };

  this.putUser = function(data) {
    data = pick(data, pickUser);
    sendToken(data);
    return request.put('v1/users/' + data.id, data);
  };

  this.postUser = function(data) {
    sendToken(data);
    return request.post('v1/users', data);
  };
}

export default UserClass;



