<template>
  <div>
    <router-link :to="{ name: 'ReportsForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> Add Report</router-link>
    <table class="table table-vertical">
      <tbody>
      <tr>
        <th>ID</th>
        <th>Name</th>
        <th>Create date</th>
        <th>Report fields</th>
        <th>Action</th>
      </tr>
      <tr v-for="report in reports">
        <td width="30">{{report.id}}</td>
        <td class="left"><router-link :to="{ name: 'ReportsItem', params: { url: 'edit', id: report.id}}">{{report.name}}</router-link></td>
        <td class="left" v-bind="reportCetaed">{{report.createdAt}}</td>
        <td class="left" width="200">
          <small v-for="field in report.reportFields"> {{field.name}}<br></small>
        </td>
        <td width="50">
          <router-link :to="{ name: 'ReportsItem', params: { url: 'edit', id: report.id}}" class="btn btn-outline btn-sm"><clr-icon shape="pencil" size="10"></clr-icon> edit</router-link>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import fecha from 'fecha';
  import { forEach } from 'lodash';

  export default {
    data() {
      return {
        reports: {}
      };
    },
    computed: {
      reportCetaed() {
        forEach(this.reports, function(value) {
          value.createdAt = fecha.format(value.createdAt, 'D/M/YYYY, H:m A');
        });
      }
    },
    mounted() {
      this.$store.dispatch('getReports', this.$store.state).then(r => {
        this.reports = r.data.reverse();
      });
    }
  };
</script>



