<template>
  <div>
    <h2>{{pageTitle}}</h2>
    <vue-form-generator :schema='schema' :model='model' :options='formOptions'></vue-form-generator>
  </div>
</template>

<script>
  import VueFormGenerator from 'vue-form-generator';
  import notification from '../../../helpers/Notification';
  import router from '../../../router/index';
  import required from '../../../helpers/Formatter/validator';

  export default {
    components: {
      'vue-form-generator': VueFormGenerator.component
    },
    data() {
      return {
        model: {
          clusterNumber: '',
          location: {
            area: '',
            district: '',
            latitude: '',
            longitude: '',
            nearestCity: '',
            province: ''
          },
          name: '',
          registeredAt: '',
          registeredById: '',
          sizeOfLand: '',
          userId: ''
        },
        schema: {
          fields: [
            {
              type: 'date-picker',
              label: 'Registration Date',
              model: 'registeredAt',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Registration By',
              model: 'registeredById',
              required: true,
              validator: required,
              values: function() {
                return this.$store.state.users;
              }
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Name of Claster',
              required: true,
              validator: required,
              model: 'name'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Cluster Number',
              required: true,
              validator: required,
              model: 'clusterNumber'
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Size of land in acres',
              model: 'sizeOfLand',
              validator: 'integer'
            },
            {
              type: 'custom-select',
              label: 'Cluster Manager',
              required: true,
              validator: required,
              model: 'userId',
              values: function() {
                return this.$store.state.users;
              }
            },
            {
              type: 'label',
              label: 'Location'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Province',
              required: true,
              validator: required,
              model: 'location.province'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'District',
              required: true,
              validator: required,
              model: 'location.district'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Area',
              required: true,
              validator: required,
              model: 'location.area'
            },
            {
              label: 'Name of nearest city',
              type: 'google-map',
              required: true,
              validator: required,
              model: 'location.nearestCity'
            },
            {
              type: 'custom-submit',
              classes: 'btn-primary',
              buttonText: 'Save',
              onSubmit: formData => this.sendData(formData)
            },
            {
              type: 'back',
              buttonText: 'Back'
            },
            {
              type: 'delete',
              entity: 'clusters',
              buttonText: 'Delete'
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      },
      pageTitle() {
        return this.pageType
          ? 'Add cluster'
          : 'Edit cluster ' + this.$route.params.id;
      },
      queryType() {
        return this.pageType ? 'postCluster' : 'putCluster';
      },
      messageType() {
        return this.pageType
          ? 'Cluster successfully added'
          : 'Cluster successfully updated';
      },
      clusterData() {
        return {
          token: this.$store.state.token,
          id: this.$route.params.id
        };
      }
    },
    methods: {
      sendData: function(data) {
        data.token = this.$store.state.token;
        this.$store
          .dispatch(this.queryType, data)
          .then(() => {
            notification.success(this.messageType);
            router.back();
          })
          .catch(e => {
            const serverError = 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    },
    mounted() {
      if (!this.pageType) {
        this.$store.dispatch('getCluster', this.clusterData).then(r => {
          this.model = Object.assign({}, r);
        });
      }
    }
  };
</script>



