<template>
  <div>
    <h2>{{pageTitle}}</h2>
    <vue-form-generator :schema='schema' :model='model' :options='formOptions'></vue-form-generator>
  </div>
</template>

<script>
  import VueFormGenerator from 'vue-form-generator';
  import notification from '../../../helpers/Notification';
  import router from '../../../router/index';
  import required from '../../../helpers/Formatter/validator';
  import formatDate from '../../../helpers/Formatter/format-date';
  import { pick, map } from 'lodash';

  const pickData = [
    'id',
    'name',
    'activityId',
    'startTime',
    'endTime',
    'reportIds',
    'activitySchedulerTargets',
    'completed',
    'failed',
    'hasProblem',
    'started'
  ];

  export default {
    components: {
      'vue-form-generator': VueFormGenerator.component
    },
    data() {
      return {
        model: {
          name: '',
          activityId: '',
          startTime: '',
          endTime: '',
          farmField: '',
          reportIds: [],
          status: ''
        },
        schema: {
          fields: [
            {
              type: 'input',
              inputType: 'text',
              label: 'Name',
              model: 'name',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              inputType: 'text',
              label: 'Activity',
              model: 'activityId',
              required: true,
              validator: required,
              values: function() {
                return this.$store.state.activitys;
              }
            },
            {
              type: 'date-picker',
              label: 'Start Date',
              model: 'startTime',
              required: true,
              validator: required
            },
            {
              type: 'date-picker',
              label: 'End Date',
              model: 'endTime',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              inputType: 'text',
              label: 'Field',
              model: 'farmField',
              values: function() {
                return this.$store.state.fields;
              }
            },
            {
              type: 'radios',
              label: 'Status',
              model: 'status',
              values: this.$store.state.status
            },
            {
              type: 'checklist',
              listBox: true,
              label: 'Reports',
              model: 'reportIds',
              required: true,
              validator: required,
              values: function() {
                return this.$store.state.reports;
              },
              checklistOptions: {
                value: 'id'
              }
            },
            {
              type: 'custom-submit',
              buttonText: 'Save',
              classes: 'btn-primary',
              onSubmit: formData => this.sendData(formData)
            },
            {
              type: 'back',
              buttonText: 'Back'
            },
            {
              type: 'delete',
              entity: 'activities/schedulers',
              buttonText: 'Delete'
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      },
      pageTitle() {
        return this.pageType
          ? 'Add activity scheduler'
          : 'Edit activity scheduler ' + this.$route.params.id;
      },
      queryType() {
        return this.pageType ? 'postActivityScheduler' : 'putActivityScheduler';
      },
      messageType() {
        return this.pageType
          ? 'New Activity Scheduler successfully added'
          : 'Activity Scheduler successfully updated';
      },
      ActivitySchedulerData() {
        return {
          token: this.$store.state.token,
          id: this.$route.params.id
        };
      }
    },
    mounted() {
      this.$store.dispatch('setReports', this.$store.state);
      this.$store.dispatch('setClusters', this.$store.state);
      this.$store.dispatch('setFields', this.$store.state);
      this.$store.dispatch('setActivitys', this.$store.state);
      if (!this.pageType) {
        this.$store
          .dispatch('getActivityScheduler', this.ActivitySchedulerData)
          .then(r => {
            this.$store.dispatch('setStatus', r.data);
            r.data = pick(r.data, pickData);
            r.data.farmField = r.data.activitySchedulerTargets[0].entityId;
            r.data.status = pick(r.data, [
              'completed',
              'failed',
              'hasProblem',
              'started'
            ]);
            r.data.status = _.map(r.data.status, (v, key) => {
              if (v == true) {
                return key;
              }
            });
            r.data.status = r.data.status.filter(x => x != undefined);
            r.data.status = r.data.status[0];
            this.model = Object.assign({}, r.data);
          });
      }
    },
    methods: {
      sendData: function(data) {
        data.token = this.$store.state.token;
        this.$store
          .dispatch(this.queryType, data)
          .then(() => {
            notification.success(this.messageType);
            router.back();
          })
          .catch(e => {
            const serverError = 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    }
  };
</script>

<style>
  .vdp-datepicker input[type=text][readonly] {
    border-bottom: 1px solid #8c8c8c !important;
  }
</style>



