<template>
  <div>
    <router-link :to="{ name: 'SchedulersForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> Add Scheduler</router-link>
    <table class="table item-list">
      <tbody>
        <tr v-for="shedulaer in shedulaers">
          <td class="left"><router-link :to="{ name: 'SchedulersItem', params: { url: 'edit', id: shedulaer.id}}">{{shedulaer.name}}</router-link></td>
          <td class="actions">
            <activitys-modal :shedulaerId="shedulaer.id"></activitys-modal>
            <router-link class="btn btn-outline btn-sm" :to="{ name: 'SchedulersItem', params: { url: 'edit', id: shedulaer.id}}">edit</router-link>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import ActivitysModal from './forms/ActivitysModal';

  export default {
    components: {
      ActivitysModal
    },
    data() {
      return {
        shedulaers: {}
      };
    },
    mounted() {
      this.$store.dispatch('getSchedulers', this.$store.state).then(r => {
        this.shedulaers = Object.assign({}, r.data);
      });
    }
  };
</script>



