          },
          landOperationRegistration: {
            lastUse: '',
            previousCrops: '',
            currentCropsSprint: '',
            currentCropsAutumn: '',
            possibleStart: '',
            currentCultivationPractice: '',
            chemicalsUsedLand: '',
            currentFarmingMachines: '',
            warehouses: '',
            isExperienceWithCultivation: '',
            isOwnManureCompost: '',
            manureCompostType: '',
            seasonalQtyManure: '',
            soilTest: '',
            landCleaningStatus: '',
            landCleaningCost: '',
            irrigationCost: '',
            powerCost: '',
            waterCost: '',
            permanentFarmStaff: '',
            seasonalFarmStaff: ''
          },
          comments: ''
        },
        schema: {
          fields: [
            {
              type: 'date-picker',
              label: 'Registration Date',
              model: 'registeredAt',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Registration By',
              model: 'registeredById',
              values: function() {
                return this.$store.state.users;
              },
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              inputType: 'text',
              label: 'Cluster',
              model: 'clusterId',
              required: true,
              validator: required,
              values: function() {
                return this.$store.state.clusters;
              }
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Name of Farm',
              model: 'name',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Farm Number',
              model: 'farmNumber',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Plot Number',
              model: 'plotNumber',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Size of farm in acres',
              model: 'sizeOfLand',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Plantation/Farm Manager',
              model: 'userId',
              values: function() {
                return this.$store.state.users;
              },
              required: true,
              validator: required
            },
            {
              type: 'label',
              label: 'Location'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Province',
              model: 'location.province',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'District',
              model: 'location.district',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Area',
              model: 'location.area',
              required: true,
              validator: required
            },
            {
              label: 'Address',
              type: 'google-map',
              model: 'location.address',
              required: true,
              validator: required
            },
            {
              type: 'label',
              label: 'Farm Description'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Geography',
              model: 'landDescriptionRegistration.geography',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Topography',
              model: 'landDescriptionRegistration.topography',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Vegetation',
              model: 'landDescriptionRegistration.vegetation',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Cimate Zone',
              model: 'landDescriptionRegistration.climateZone',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Weather conditions',
              model: 'landDescriptionRegistration.weather'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Rain season period for spring season',
              model: 'landDescriptionRegistration.rainSeasonSpring'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Rain season period for autumn season',
              model: 'landDescriptionRegistration.rainSeasonAutumn'
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Average rainfall per annum in mm',
              model: 'landDescriptionRegistration.rainfallAvg'
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Average temperature per annum in Celsius degrees',
              model: 'landDescriptionRegistration.tempAvg'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Soil type',
              model: 'landDescriptionRegistration.soilType'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'pH - value in soil',
              model: 'landDescriptionRegistration.phValue'
            },
            {
              type: 'custom-select',
              label: 'Virgin land (yes/no)',
              model: 'landDescriptionRegistration.isVirginLand',
              values: function() {
                return [{ id: 'true', name: 'Yes' }, { id: 'false', name: 'No' }];
              },
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Organic certified land (yes/no)',
              model: 'landDescriptionRegistration.isOrganicCertified',
              values: function() {
                return [{ id: 'true', name: 'Yes' }, { id: 'false', name: 'No' }];
              },
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Fair trade certified land (yes/no)',
              model: 'landDescriptionRegistration.isFairTradeCertified',
              values: function() {
                return [{ id: 'true', name: 'Yes' }, { id: 'false', name: 'No' }];
              },
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Certification by which agency(ies)',
              model: 'landDescriptionRegistration.certificationAgency'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Name of nearest port',
              model: 'landDescriptionRegistration.nearestPort'
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Distance to nearest port in km',
              model: 'landDescriptionRegistration.nearestPortDistance'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Name of nearest airport',
              model: 'landDescriptionRegistration.nearestAirport'
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Distance to nearest port in km',
              model: 'landDescriptionRegistration.nearestAirportDistance'
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Distance to main road in km',
              model: 'landDescriptionRegistration.mainRoadDistance'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'List the current water sources',
              model: 'landDescriptionRegistration.waterSources',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Type of irrigation system',
              model: 'landDescriptionRegistration.irrigationSystem',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Access to power grid system (yes/no)',
              model: 'landDescriptionRegistration.isPowerGridSystem',
              values: function() {
                return [{ id: 'true', name: 'Yes' }, { id: 'false', name: 'No' }];
              },
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Connected to national power grid system (yes/no)',
              model: 'landDescriptionRegistration.isNationalPowerGridSystem',
              values: function() {
                return [{ id: 'true', name: 'Yes' }, { id: 'false', name: 'No' }];
              },
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Regulations of the land, if any',
              model: 'landDescriptionRegistration.landRegulations',
              required: true,
              validator: required
            },
            {
              type: 'label',
              label: 'Farm Operations'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Last use of the land',
              model: 'landOperationRegistration.lastUse',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Previous crops cultivated on the land',
              model: 'landOperationRegistration.previousCrops',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Current crops spring season',
              model: 'landOperationRegistration.currentCropsSprint',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Current crops autumn season',
              model: 'landOperationRegistration.currentCropsAutumn',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'First possible start of cultivation',
              model: 'landOperationRegistration.possibleStart',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Current cultivation practice conventional/organic',
              model: 'landOperationRegistration.currentCultivationPractice',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label:
                'Specify pesticides, insecticides, other chemicals used on the land',
              model: 'landOperationRegistration.chemicalsUsedLand'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Specify current farming machines available on the farm',
              model: 'landOperationRegistration.currentFarmingMachines'
            },
            {
              type: 'input',
              inputType: 'text',
              label:
                'Specify warehousing facilities for temporary storage of crops',
              model: 'landOperationRegistration.warehouses'
            },
            {
              type: 'input',
              inputType: 'text',
              label:
                'Confirm if the farm has experience within cotton/soybean/manioc cultivation',
              model: 'landOperationRegistration.isExperienceWithCultivation',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Specify whether the farm produce own manure and compost',
              model: 'landOperationRegistration.isOwnManureCompost',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label:
                'Specify the type of manure and compost produced on farm, if any',
              model: 'landOperationRegistration.manureCompostType',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label:
                'Specify the seasonal QTY of manure produced on farm, if any',
              model: 'landOperationRegistration.seasonalQtyManure'
            },
            {
              type: 'custom-select',
              label: 'Soil test',
              model: 'landOperationRegistration.soilTest',
              values: function() {
                return [
                  { id: 'SOIL_TEST_TESTING', name: 'Testing' },
                  { id: 'SOIL_TEST_PREPARED', name: 'Prepared' },
                  { id: 'SOIL_TEST_COMPLETED', name: 'Completed' }
                ];
              },
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Land cleaning status',
              model: 'landOperationRegistration.landCleaningStatus',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Land cleaning cost for total land',
              model: 'landOperationRegistration.landCleaningCost'
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Irrigation cost for total land',
              model: 'landOperationRegistration.irrigationCost'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Power establishment cost',
              model: 'landOperationRegistration.powerCost'
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Water connection cost',
              model: 'landOperationRegistration.waterCost'
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Number of permanent farm staff',
              model: 'landOperationRegistration.permanentFarmStaff'
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Number of seasonal farm staff',
              model: 'landOperationRegistration.seasonalFarmStaff'
            },
            {
              type: 'textArea',
              inputType: 'text',
              label: 'General Comments',
              model: 'comments',
              rows: 4
            },
            {
              type: 'custom-submit',
              buttonText: 'Save',
              classes: 'btn-primary',
              onSubmit: formData => this.sendData(formData)
            },
            {
              type: 'back',
              buttonText: 'Back'
            },
            {
              type: 'delete',
              entity: 'farms',
              buttonText: 'Delete'
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      },
      pageTitle() {
        return this.pageType ? 'Add farm' : 'Edit farm ' + this.$route.params.id;
      },
      queryType() {
        return this.pageType ? 'postFarm' : 'putFarm';
      },
      messageType() {
        return this.pageType
          ? 'Farm successfully added'
          : 'Farm successfully updated';
      },
      farmData() {
        return {
          token: this.$store.state.token,
          id: this.$route.params.id
        };
      }
    },
    methods: {
      sendData: function(data) {
        data.token = this.$store.state.token;
        this.$store
          .dispatch(this.queryType, data)
          .then(() => {
            notification.success(this.messageType);
            router.back();
          })
          .catch(e => {
            const serverError = 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    },
    mounted() {
      if (!this.pageType) {
        this.$store.dispatch('getFarm', this.farmData).then(r => {
          this.model = Object.assign({}, r.data);
        });
      }
      this.$store.dispatch('setClusters', this.$store.state);
    }
  };
</script>



