<template>
  <div class='login-wrapper'>
    <div class='login'>
      <div class="title">
        <clr-icon shape="grid-view" size="31"></clr-icon>
        <span>Agritex System</span>
      </div>
      <vue-form-generator @validated='onValidated' :schema='schema' :model='model' :options='formOptions'></vue-form-generator>
    </div>
  </div>
</template>

<script>
  import VueFormGenerator from 'vue-form-generator';
  import validators from 'vue-form-generator';
  import notification from '../helpers/Notification';
  import router from '../router/index';

  export default {
    components: {
      'vue-form-generator': VueFormGenerator.component
    },
    data() {
      return {
        model: {
          username: '',
          password: ''
        },
        schema: {
          fields: [
            {
              type: 'input',
              inputType: 'text',
              placeholder: 'Login',
              inputName: 'login',
              model: 'username',
              required: true,
              min: 3,
              validator: validators.validators.string.locale({
                fieldIsRequired: '',
                textTooSmall: 'Login must be at least {1} characters'
              })
            },
            {
              type: 'input',
              inputType: 'password',
              placeholder: 'Password',
              model: 'password',
              required: true,
              min: 6,
              validator: validators.validators.string.locale({
                fieldIsRequired: '',
                textTooSmall: 'Password must be at least {1} characters'
              })
            },
            {
              type: 'custom-submit',
              buttonText: 'Submit',
              classes: 'btn-primary',
              onSubmit: e => {
                this.submitAction(e);
              }
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    created() {
      if (this.$store.state.token) {
        router.push({ name: 'calendar', params: this.$route.params });
      }
    },
    mounted() {
      this.$store.state.spinner = false;
    },
    methods: {
      submitAction: function(data) {
        this.$store
          .dispatch('loginUser', data)
          .then(r => {
            notification.success('You are Login');
            router.push({ name: 'calendar', params: this.$route.params });
          })
          .then(() => {
            this.$store.dispatch('setUsers', this.$store.state);
          })
          .catch(e => {
            let serverError =
              e.status === 401
                ? 'Incorrect user or password'
                : 'Undefined error on server.';
            notification.error(serverError);
          });
      },
      onValidated(isValid, error) {}
    }
  };
</script>

<style>
  .login .vue-form-generator,
  .login .vue-form-generator .form-group label{
    padding-top: 0;
  }

  .login label {
    display: none;
  }

  .login .title {
    margin-bottom: 70px;
  }

  .login .title span {
    position: relative;
    top:3px;
  }

  .login .vue-form-generator .form-group .field-wrap,
  .login .btn, .login .field-custom-submit {
    width: 100% !important;
  }

  .login .form-group {
    margin-bottom: 40px !important;
  }

  .login-wrapper .required .field-wrap:after {
    display: none;
  }
</style>



