<template>
  <div>
    <router-link :to="{ name: 'ClustersForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> Add Cluster</router-link>
    <table class="table item-list">
      <tbody>
        <tr v-for="cluster in clusters">
          <td class="left"><router-link :to="{ name: 'ClusterItem', params: { url: 'edit', id: cluster.id}}">{{cluster.name}}</router-link></td>
          <td class="actions"><router-link class="btn btn-outline btn-sm" :to="{ name: 'ClusterItem', params: { url: 'edit', id: cluster.id}}">edit</router-link></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        clusters: {}
      };
    },
    mounted() {
      this.$store.dispatch('getClusters', this.$store.state).then(r => {
        this.clusters = Object.assign({}, r.data);
      });
    }
  };
</script>



