import request from '../HttpRequest';
import sendToken from '../HttpRequest/send-token';
import timeStampFormatter from '../../helpers/Formatter/timestamp';

function SeasonsClass() {
  this.getSchedulers = function(data) {
    sendToken(data);
    return request.get('v1/seasons/schedulers');
  };
  this.getSeasonsTypes = function(data) {
    sendToken(data);
    return request.get('v1/seasons');
  };
  this.getSeasonsType = function(data) {
    sendToken(data);
    return request.get('v1/seasons/' + data.id);
  };
  this.postSeasonsType = function(data) {
    sendToken(data);
    return request.post('v1/seasons', data);
  };
  this.putSeasonsType = function(data) {
    sendToken(data);
    return request.put('v1/seasons/' + data.id, data);
  };
  this.getFarmByClusterId = function(data) {
    sendToken(data);
    return request.get('v1/clusters/' + data.id + '/farms', data);
  };
  this.getSeasonScheduler = function(data) {
    sendToken(data);
    return request.get('v1/seasons/schedulers/' + data.id);
  };
  this.postSeasonScheduler = function(data) {
    sendToken(data);
    data.endTime = timeStampFormatter(data.endTime);
    data.startTime = timeStampFormatter(data.startTime);
    return request.post('v1/seasons/schedulers', data);
  };
  this.putSeasonScheduler = function(data) {
    sendToken(data);
    return request.put('v1/seasons/schedulers/' + data.id, data);
  };
  this.assignActivity = function(data) {
    sendToken(data);
    return request.put(
      'v1/seasons/schedulers/' + data.id + '/activity_schedulers',
      data.activitySchedulersId
    );
  };
  this.getAssignActivity = function(data) {
    sendToken(data);
    return request.get(
      'v1/seasons/schedulers/' + data.id + '/activity_schedulers'
    );
  };
}

export default SeasonsClass;



