<template>
  <div>
    <h2>{{pageTitle}}</h2>
    <vue-form-generator :schema='schema' :model='model' :options='formOptions'></vue-form-generator>
  </div>
</template>

<script>
  import VueFormGenerator from 'vue-form-generator';
  import notification from '../../../helpers/Notification';
  import router from '../../../router/index';
  import required from '../../../helpers/Formatter/validator';

  export default {
    components: {
      'vue-form-generator': VueFormGenerator.component
    },
    data() {
      return {
        model: {
          name: '',
          clusterId: '',
          farmId: '',
          userId: '',
          registered_by: '',
          registration_date: '',
          location: {
            province: '',
            district: '',
            area: '',
            nearest_city: '',
            address: '',
            latitude: '',
            longitude: ''
          },
          plot_: '',
          size_in_acres: '',
          descriptions: {
            geography: '',
            topography: '',
            vegetation: '',
            soil_type: '',
            ph_value: '',
            is_virgin: '',
            is_organic_certified: '',
            is_fair_trade_certified: '',
            certification_agency: '',
            water_sources: '',
            irrigation_system: '',
            land_regulations: ''
          },
          general_comments: ''
        },
        schema: {
          fields: [
            {
              type: 'date-picker',
              label: 'Registration Date',
              model: 'registeredAt',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Registration By',
              model: 'registeredById',
              values: function() {
                return this.$store.state.users;
              },
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              inputType: 'text',
              label: 'Farm',
              model: 'farmId',
              values: function() {
                return this.$store.state.farms;
              },
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Name of Field',
              model: 'name',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Plot Number',
              model: 'plotNumber',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Size of field in acres',
              model: 'sizeOfLand',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Field Officer',
              model: 'userId',
              values: function() {
                return this.$store.state.users;
              },
              required: true,
              validator: required
            },
            {
              type: 'label',
              label: 'Location'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Province',
              model: 'location.province',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'District',
              model: 'location.district',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Area',
              model: 'location.area',
              required: true,
              validator: required
            },
            {
              label: 'Address',
              type: 'google-map',
              model: 'location.address',
              required: true,
              validator: required
            },
            {
              type: 'label',
              label: 'Field Description'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Geography',
              model: 'fieldDescriptionRegistration.geography'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Topography',
              model: 'fieldDescriptionRegistration.topography'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Vegetation',
              model: 'fieldDescriptionRegistration.vegetation'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Soil type',
              model: 'fieldDescriptionRegistration.soilType',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'pH - value in soil',
              model: 'fieldDescriptionRegistration.phValue',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Virgin land (yes/no)',
              model: 'fieldDescriptionRegistration.isVirginLand',
              values: function() {
                return [{ id: 'true', name: 'Yes' }, { id: 'false', name: 'No' }];
              },
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Organic certified land (yes/no)',
              model: 'fieldDescriptionRegistration.isOrganicCertified',
              values: function() {
                return [{ id: 'true', name: 'Yes' }, { id: 'false', name: 'No' }];
              },
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Fair trade certified land (yes/no)',
              model: 'fieldDescriptionRegistration.isFairTradeCertified',
              values: function() {
                return [{ id: 'true', name: 'Yes' }, { id: 'false', name: 'No' }];
              },
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Certification by which agency(ies)',
              model: 'fieldDescriptionRegistration.certificationAgency',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'List the current water sources',
              model: 'fieldDescriptionRegistration.waterSources',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Type of irrigation system',
              model: 'fieldDescriptionRegistration.irrigationSystem',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Regulations of the land, if any',
              model: 'fieldDescriptionRegistration.landRegulations',
              required: true,
              validator: required
            },
            {
              type: 'label',
              label: 'Field Operations'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Last use of the land',
              model: 'fieldOperationRegistration.lastUse',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Current cultivation practice conventional/organic',
              model: 'fieldOperationRegistration.currentCultivationPractice',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label:
                'Specify pesticides, insecticides, other chemicals used on the land',
              model: 'fieldOperationRegistration.chemicalsUsedLand',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Current crops spring season',
              model: 'fieldOperationRegistration.currentCropsSprint',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Current crops autumn season',
              model: 'fieldOperationRegistration.currentCropsAutumn',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'First possible start of cultivation',
              model: 'fieldOperationRegistration.possibleStart',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Soil test',
              model: 'fieldOperationRegistration.soilTest',
              values: function() {
                return [
                  { id: 'SOIL_TEST_TESTING', name: 'Testing' },
                  { id: 'SOIL_TEST_PREPARED', name: 'Prepared' },
                  { id: 'SOIL_TEST_COMPLETED', name: 'Completed' }
                ];
              },
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Land cleaning status',
              model: 'fieldOperationRegistration.landCleaningStatus',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Land cleaning cost for total land',
              model: 'fieldOperationRegistration.landCleaningCost',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Irrigation cost for total land',
              model: 'fieldOperationRegistration.irrigationCost',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'number',
              label: 'Number of permanent farm staff',
              model: 'fieldOperationRegistration.permanentFarmStaff'
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Number of seasonal farm staff',
              model: 'fieldOperationRegistration.seasonalFarmStaff'
            },
            {
              type: 'textArea',
              inputType: 'text',
              label: 'General Comments',
              model: 'comments',
              rows: 4
            },
            {
              type: 'custom-submit',
              buttonText: 'Save',
              classes: 'btn-primary',
              onSubmit: formData => this.sendData(formData)
            },
            {
              type: 'back',
              buttonText: 'Back'
            },
            {
              type: 'delete',
              entity: 'farm-fields',
              buttonText: 'Delete'
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      },
      pageTitle() {
        return this.pageType
          ? 'Add field'
          : 'Edit field ' + this.$route.params.id;
      },
      queryType() {
        return this.pageType ? 'postField' : 'putField';
      },
      messageType() {
        return this.pageType
          ? 'Field successfully added'
          : 'Field successfully updated';
      },
      fieldData() {
        return {
          token: this.$store.state.token,
          id: this.$route.params.id
        };
      }
    },
    methods: {
      sendData: function(data) {
        data.token = this.$store.state.token;
        this.$store
          .dispatch(this.queryType, data)
          .then(() => {
            notification.success(this.messageType);
            router.back();
          })
          .catch(e => {
            const serverError = 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    },
    mounted() {
      if (!this.pageType) {
        this.$store.dispatch('getField', this.fieldData).then(r => {
          this.model = Object.assign({}, r.data);
        });
      }
      this.$store.dispatch('setFarms', this.$store.state);
    }
  };
</script>



