<template>
  <div>
    <router-link :to="{ name: 'UsersForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> Add User</router-link>
    <table class="table item-list">
      <tbody>
      <tr v-for="user in users">
        <td class="left"><router-link :to="{ name: 'UsersItem', params: { url: 'edit', id: user.id}}">{{user.firstName}} {{user.lastName}}</router-link></td>
        <td class="left"><span class="label" v-for="role in user.roles"><span v-bind="roleFormatName">{{role.name}}</span></span> </td>
        <td class="actions"><router-link :to="{ name: 'UsersItem', params: { url: 'edit', id: user.id}}" class="btn btn-outline btn-sm">edit</router-link></td>
      </tr>
      </tbody>
    </table>
  </div>
</template>


<script>
  import { forEach } from 'lodash';
  import roleNameFormatter from '../../helpers/Formatter/roles';

  export default {
    data() {
      return {
        users: []
      };
    },
    computed: {
      roleFormatName() {
        forEach(this.users, function(value) {
          forEach(value.roles, function(value) {
            value.name = roleNameFormatter(value.name);
          });
        });
      }
    },
    mounted() {
      this.$store.dispatch('getUsers', this.$store.state).then(r => {
        this.users = r.data;
      });
    }
  };
</script>



