<template>
  <div>
    <router-link :to="{ name: 'RolesForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> Add Role</router-link>
    <table class="table item-list">
      <tbody>
      <tr v-for="role in roles">
        <td class="left" v-bind="roleFormatName"><router-link :to="{ name: 'RolesItem', params: { url: 'edit', id: role.id}}">{{role.name}}</router-link></td>
        <td class="actions"><router-link :to="{ name: 'RolesItem', params: { url: 'edit', id: role.id}}" class="btn btn-outline btn-sm">edit</router-link></td>
      </tr>
      </tbody></table>
  </div>
</template>


<script>
  import { forEach } from 'lodash';
  import roleNameFormatter from '../../helpers/Formatter/roles';

  export default {
    data() {
      return {
        roles: {}
      };
    },
    computed: {
      roleFormatName() {
        forEach(this.roles, function(value) {
          value.name = roleNameFormatter(value.name);
        });
      }
    },
    mounted() {
      this.$store.dispatch('getRoles', this.$store.state).then(r => {
        this.roles = Object.assign({}, r.data);
      });
    }
  };
</script>



