<template>
  <div>
    <router-link :to="{ name: 'FieldsForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> Add Field</router-link>
    <table class="table item-list">
      <tbody>
        <tr v-for="field in fields">
          <td class="left"><router-link :to="{ name: 'FieldsItem', params: { url: 'edit', id: field.id}}">{{field.name}}</router-link></td>
          <td class="actions"><router-link class="btn btn-outline btn-sm" :to="{ name: 'FieldsItem', params: { url: 'edit', id: field.id}}">edit</router-link></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        fields: {}
      };
    },
    mounted() {
      this.$store.dispatch('getFields', this.$store.state).then(r => {
        this.fields = Object.assign({}, r.data);
      });
    }
  };
</script>



