<template>
  <input class="btn" type='submit' :value='schema.buttonText' @click='click' :name='schema.inputName' :disabled='disabled'/>
</template>

<script>
  import { abstractField } from 'vue-form-generator';
  import router from '../../router/index';

  export default {
    mixins: [abstractField],

    methods: {
      click() {
        return router.back();
      }
    }
  };
</script>



