<template>
  <div>
    <router-link :to="{ name: 'ActivitySchedulersForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> ADD Scheduler</router-link>
    <table class="table item-list">
      <tbody>
        <tr v-for="scheduler in schedulers">
          <td class="left"><router-link :to="{ name: 'ActivitySchedulersItem', params: { url: 'edit', id: scheduler.id}}">{{scheduler.name}}</router-link></td>
          <td class="actions"><router-link :to="{ name: 'ActivitySchedulersItem', params: { url: 'edit', id: scheduler.id}}" class="btn btn-outline btn-sm">edit</router-link></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        schedulers: {}
      };
    },
    mounted() {
      this.$store.dispatch('getActivitySchedulers', this.$store.state).then(r => {
        this.schedulers = Object.assign({}, r.data);
      });
    }
  };
</script>



