<template>
  <div>
    <router-link :to="{ name: 'ActivityTypesForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> Add Category</router-link>
    <table class="table item-list">
      <tbody>
        <tr v-for="type in types">
          <td class="left"><router-link :to="{ name: 'ActivityTypesItem', params: { url: 'edit', id: type.id}}">{{type.name}}</router-link></td>
          <td class="actions"><router-link :to="{ name: 'ActivityTypesItem', params: { url: 'edit', id: type.id}}" class="btn btn-outline btn-sm">edit</router-link></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        types: {}
      };
    },
    mounted() {
      this.$store.dispatch('getActivityTypes', this.$store.state).then(r => {
        this.types = Object.assign({}, r.data);
      });
    }
  };
</script>



