<template>
  <div>
    <router-link :to="{ name: 'TypesForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> Add Season</router-link>
    <table class="table item-list">
      <tbody>
        <tr v-for="type in types">
          <td class="left"><router-link :to="{ name: 'TypesItem', params: { url: 'edit', id: type.id }}">{{type.name}}</router-link></td>
          <td class="actions"><router-link class="btn btn-outline btn-sm" :to="{ name: 'TypesItem', params: { url: 'edit', id: type.id }}">edit</router-link></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        types: {}
      };
    },
    mounted() {
      this.$store.dispatch('getSeasonsTypes', this.$store.state).then(r => {
        this.types = Object.assign({}, r.data);
      });
    }
  };
</script>



