<template>
  <input v-if="!pageType" class="f-right btn btn-danger-outline" type='submit' :value='schema.buttonText' @click='click' :name='schema.inputName' :disabled='disabled'/>
</template>

<script>
  import { abstractField } from 'vue-form-generator';
  import notification from '../../helpers/Notification';
  import router from '../../router/index';

  export default {
    mixins: [abstractField],

    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      }
    },
    methods: {
      click() {
        const entityData = {
          entity: this.schema.entity,
          token: this.$store.state.token,
          id: [this.$route.params.id]
        };
        this.$store
          .dispatch('deleteItem', entityData)
          .then(() => {
            notification.success('Successfully delete');
            router.back();
          })
          .catch(e => {
            const serverError = 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    }
  };
</script>



