<template>
  <div>
    <div class="row">
      <alerts v-if='eventsStatus.length > 0' v-for="(item, index) in eventsStatus[0]" :status="index" :key="item"></alerts>
    </div>
    <vue-event-calendar
      :events="events"
      @day-changed="handleDayChanged"
      @month-changed="handleMonthChanged"
    >
      <template scope="props">
        <div v-for="(event, index) in props.showEvents" class="event-item" v-bind:class="{ completed: event.status.completed, 'failed': event.status.failed, 'hasproblem': event.status.hasProblem, 'started': event.status.started }">
          <div class="wrapper">
            <h3 class="title"><router-link :to="{ name: 'ActivitySchedulersItem', params: { url: 'edit', id: event.id}}">{{event.title}}</router-link></h3>
            <p class="time">Start: {{event.formatDate}} - End: {{event.formatEnd}}</p>
            <p class="desc">{{event.desc}}</p>
            <button class="btn btn-success-outline btn-sm">Complete</button>
            <router-link class="btn btn-info-outline btn-sm" :to="{ name: 'ActivitySchedulersItem', params: { url: 'edit', id: event.id}}">Edit</router-link>
            <router-link class="btn btn-danger-outline btn-sm" :to="{ name: 'ActivitySchedulersItem', params: { url: 'edit', id: event.id}}">Delete</router-link>
          </div>
        </div>
      </template>
    </vue-event-calendar>
  </div>
</template>


<script>
  import fecha from 'fecha';
  import router from '../router/index';
  import Alerts from './dashboard/Alerts';

  const prepareEvents = function(data) {
    return {
      date: fecha.format(data.startTime, 'YYYY/M/D'),
      formatDate: fecha.format(data.startTime, 'D/MM/YYYY'),
      formatEnd: fecha.format(data.endTime, 'D/MM/YYYY'),
      title: data.name,
      desc: data.activity.name,
      id: data.id,
      status: {
        hasProblem: data.hasProblem,
        failed: data.failed,
        completed: data.completed,
        started: data.started
      }
    };
  };

  export default {
    components: {
      Alerts
    },
    data() {
      return {
        events: [],
        today: new Date()
      };
    },
    computed: {
      firstDay() {
        return new Date(this.today.getFullYear(), this.today.getMonth(), 1);
      },
      lastDay() {
        return new Date(this.today.getFullYear(), this.today.getMonth() + 1, 0);
      },
      eventsStatus() {
        return this.events.map(x => x.status);
      }
    },
    methods: {
      handleDayChanged(data) {},
      handleMonthChanged(data) {
        console.log(data);
        data = data.split('/');
        data = new Date(data[1] + '-' + data[0] + '-01 00:00:00');
        this.today = data;
        this.getEvents();
      },
      getEvents() {
        const sendData = {
          token: this.$store.state.token,
          startDate: this.firstDay.getTime(),
          endDate: this.lastDay.getTime()
        };
        this.$store.dispatch('getEvents', sendData).then(r => {
          this.events = r.data.map(x => prepareEvents(x));
        });
      }
    },
    mounted() {
      this.$EventCalendar.toDate(fecha.format(Date.now(), 'YYYY/M/D'));
      this.getEvents();
    }
  };
</script>

<style>
  .table.calendar {
    border:0;
  }
  .table.calendar th {
    background: #fafafa;
    border: 0;
    border-bottom: 1px solid #eee;
    font-size:14px;
  }

  .table.calendar td {
    background: #fafafa;
    border-bottom: 1px solid #eee;
    border-right: 1px solid #eee;
    width: 14.28%;
  }

  .table.calendar tbody td:first-child {
    border-left: 1px solid #eee;
  }

  .table.calendar td span {
    opacity:0.5;
    min-height: 70px;
    display: flex;
    text-align: right;
    font-size: 16px;
  }

  .table.calendar td:hover,
  .table.calendar td:focus{
    background: #ededed;
  }

  .table.calendar small {
    color: #cccccc;
  }

  .table.calendar td.active {
    background: #e8fde7;
  }

  .table.calendar td.active span {
    font-size: 1.9em;
  }

  .table.calendar td.weekend span {
    color: #8b0000;
  }

  /* New style for calendar block */
  .__vev_calendar-wrapper {
    max-width: 100% !important;
    margin-top: 2rem !important;
  }

  .__vev_calendar-wrapper .events-wrapper,
  .__vev_calendar-wrapper .events-wrapper .event-item,
  .__vev_calendar-wrapper .events-wrapper .date {
    border-radius: 0 !important;
    margin-top: 10px !important;
    font-size: 16px !important;
    padding: 5px 10px !important;
    line-height: 33px !important;
    max-width: 99% !important;
  }

  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item .is-today {
    width: 7px !important;
    height: 7px !important;
    left: 53% !important;
    top: 53% !important;
  }

  .wrapper .time {
    font-size: 11px !important;
    right: 20px !important;
    top: 12px !important;
  }

  .__vev_calendar-wrapper ::-webkit-scrollbar-thumb {
    display: none !important;
  }

  .__vev_calendar-wrapper .events-wrapper .event-item .title {
    height: 33px !important;
    font-weight: 500 !important;
  }
  .__vev_calendar-wrapper .events-wrapper .event-item .title {
    line-height: 28px !important;
  }

  /*Change plugin Layout*/
  .__vev_calendar-wrapper .cal-wrapper {
    /*width: 100% !important;*/
    padding: 0 !important;
  }

  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item .date-num {
    font-size: 0.9rem !important;
    padding-left: 8px !important;
    position: relative !important;
    z-index: 3;
    height: 7rem !important;
    border: 1px solid #e1e1e1 !important;
    border-left: 0 !important;
    border-top: 0 !important;
    text-align: left !important;
