    text-align: center !important;
    font-size: 1rem !important;
    font-weight: bold !important;
    border: 1px solid #e1e1e1 !important;
    background: #f1f1f1 !important;
  }

  .__vev_calendar-wrapper .events-wrapper {
    color: #fff;
    padding: 40px 45p;
    ft: 50%;
    bottom: 0;
    padding: 20px 10px !important;
    margin-top: 30px !important;
    top: 18px !important;
  }

  .__vev_calendar-wrapper .events-wrapper .date {
    display: none !important;
  }

  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item.event {
    /*has event*/
  }

  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item.today.event,
  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item.today {
    background: #f1f1f1 !important;
  }

  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item.event.selected-day {
