  }

  .__vev_calendar-wrapper .cal-wrapper .cal-body .weeks .item {
    line-height: 34px !important;
  }

  .event-item.completed {
    background: #f0ffef !important;
  }

  .event-item.completed h3 a {
    color: #266900 !important;
  }

  .event-item.failed {
    background: #f9eeee !important;
  }

  .event-item.failed h3 a {
    color: #d43b3b !important;
  }

  .__vev_calendar-wrapper .events-wrapper .event-item .desc {
    color: #353535 !important;
  }

