  }

  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item.event.failed.selected-day span {
    color: #fff !important;
  }


/*hasproblem*/
  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item.event.hasproblem {
    background: #fcfde5 !important;
  }

  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item.event.hasproblem  .is-event,
  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item.event.hasproblem  span{
    border-color: #e6a452 !important;
    color: #e6a452 !important;
  }

  .__vev_calendar-wrapper .cal-wrapper .cal-body .dates .item.event.hasproblem.selected-day  .is-event {
