<template>
  <div>
    <datepicker @input="eventValue" :value="dateValue" :disabled-picker="schema.disabled"></datepicker>
  </div>
</template>

<script>
  import fecha from 'fecha';
  import { abstractField } from 'vue-form-generator';
  import Datepicker from 'vuejs-datepicker';

  export default {
    mixins: [abstractField],
    components: {
      Datepicker
    },
    data() {
      return {
        formatValueToModel: '',
        dateValue: ''
      };
    },
    methods: {
      eventValue(val) {
        this.value = val;
      }
    },
    watch: {
      value: function(data) {
        this.dateValue = fecha.format(data, 'D MMM YYYY');
      }
    }
  };
</script>

<style>
  .vdp-datepicker header {
    background: #004a70;
    height:40px;
  }
  .vdp-datepicker__calendar header .next:after{
    border-left-color: #fff !important;
  }
  .vdp-datepicker__calendar header .prev:after{
    border-right-color: #fff !important;
  }
  .vdp-datepicker__calendar header .next:not(.disabled):hover, .vdp-datepicker__calendar header .prev:not(.disabled):hover, .vdp-datepicker__calendar header .up:not(.disabled):hover {
    background: none !important;
  }
  .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).day:hover, .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).month:hover, .vdp-datepicker__calendar .cell:not(.blank):not(.disabled).year:hover {
    border: 1px solid #565656;
  }
  .vdp-datepicker__calendar .cell.selected, .vdp-datepicker__calendar .cell.selected.highlighted, .vdp-datepicker__calendar .cell.selected:hover {
    background: #e1f1f6;
  }

</style>



