<template>
  <div>
    <h2>{{ pageTitle }}</h2>
    <vue-form-generator :schema='schema' :model='model' :options='formOptions'></vue-form-generator>
  </div>
</template>

<script>
  import VueFormGenerator from 'vue-form-generator';
  import router from '../../../router/index';
  import notification from '../../../helpers/Notification';
  import fecha from 'fecha';
  import { forEach } from 'lodash';
  import required from '../../../helpers/Formatter/validator';

  export default {
    components: {
      'vue-form-generator': VueFormGenerator.component
    },
    data() {
      return {
        model: {
          name: '',
          description: '',
          startTime: '',
          endTime: ''
        },
        schema: {
          fields: [
            {
              type: 'input',
              inputType: 'text',
              label: 'Name',
              model: 'name',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Select Season',
              required: true,
              validator: required,
              model: 'seasonId',
              values: function() {
                return this.$store.state.seasonsType;
              }
            },
            {
              type: 'date-picker',
              label: 'Start Date',
              model: 'startTime',
              required: true,
              validator: required
            },
            {
              type: 'date-picker',
              label: 'End Date',
              model: 'endTime',
              required: true,
              validator: required
            },
            {
              type: 'textArea',
              rows: 4,
              label: 'Description',
              model: 'description',
              required: true,
              validator: required
            },
            {
              type: 'custom-submit',
              buttonText: 'Save',
              classes: 'btn-primary',
              onSubmit: formData => this.sendData(formData)
            },
            {
              type: 'back',
              buttonText: 'Back'
            },
            {
              type: 'delete',
              entity: 'seasons/schedulers',
              buttonText: 'Delete'
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      },
      pageTitle() {
        return this.pageType
          ? 'Add season scheduler'
          : 'Edit season scheduler ' + this.$route.params.id;
      },
      queryType() {
        return this.pageType ? 'postSeasonScheduler' : 'putSeasonScheduler';
      },
      messageType() {
        return this.pageType
          ? 'Season scheduler successfully added'
          : 'Season scheduler successfully updated';
      },
      schedulerData() {
        return {
          token: this.$store.state.token,
          id: this.$route.params.id
        };
      }
    },
    mounted() {
      this.$store.dispatch('setSeasonsType', this.$store.state);
      this.$store.dispatch('setActivitys', this.$store.state);
      if (!this.pageType) {
        this.$store.dispatch('getSeasonScheduler', this.schedulerData).then(r => {
          this.model = Object.assign({}, r.data);
        });
      }
    },
    methods: {
      sendData: function(data) {
        data.token = this.$store.state.token;
        this.$store
          .dispatch(this.queryType, data)
          .then(() => {
            notification.success(this.messageType);
            router.back();
          })
          .catch(e => {
            const serverError = 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    }
  };
</script>



