<template>
  <div class='select form-control'>
    <select v-model="value" :disabled="disabled" :name="schema.inputName" class="form-control" @change="onChange">
      <option :disabled="schema.required" :value="null" :selected="value == undefined">{{ selectOptions.noneSelectedText || "Select..." }}</option>
      <option v-for="item in items" :value="getItemID(item)">{{ getItemName(item) }}</option>
    </select>
  </div>
</template>

<script>
  import { isObject } from 'lodash';
  import { isFunction } from 'lodash';
  import { abstractField } from 'vue-form-generator';

  export default {
    mixins: [abstractField],
    data() {
      return {
        formatValueToModel: ''
      };
    },

    computed: {
      selectOptions() {
        return this.schema.selectOptions || {};
      },
      items() {
        let values = this.schema.values;
        if (typeof values === 'function') {
          return values.apply(this, [this.model, this.schema]);
        } else {
          return values;
        }
      }
    },
    methods: {
      getItemID(item) {
        if (isObject(item) && item.id) {
          return item.id;
        }
        return item;
      },
      getItemName(item) {
        if (isObject(item) && item.name) {
          return item.name;
        }
        return item;
      },
      onChange() {
        if (this.schema.validateBeforeSubmit === true) {
          if (!this.$parent.validate()) {
            return;
          }
        }
        if (isFunction(this.schema.onChange)) {
          this.schema.onChange(this.model, this.schema);
        }
      }
    }
  };
</script>



