<template>
  <div id="app">
    <router-view></router-view>
    <spinner v-show="spinnerStatus"></spinner>
  </div>
</template>


<script>
  import spinner from './helpers/Spiner';

  export default {
    components: {
      spinner
    },
    computed: {
      spinnerStatus() {
        return this.$store.state.spinner;
      }
    }
  };
</script>



