<template>
  <div class="card card--activity">
    <div class="card-header">Activity #{{indexInArray + 1}}</div>
    <div class="card-block">
      <div class="card-text">
        <div class="row">
          <div class="col-lg-6">
            <div class="form-group row">
              <div class="col-lg-3 col-md-4 col-sm-4 col-xs-12">
                <label>Activity</label>
              </div>
              <div class="col-lg-9 col-md-8 col-sm-8 col-xs-12">
                <div class="select form-control">
                  <select @change="getActivityId" v-model="getData.activityId">
                    <option>Please select</option>
                    <option v-for="activity in $store.state.activity" :value="activity.id">{{activity.name}}</option>
                  </select>
                </div>
              </div>
            </div>
            <div class="form-group row">
              <div class="col-lg-3 col-md-4 col-sm-4 col-xs-12">
                <label>Field</label>
              </div>
              <div class="col-lg-9 col-md-8 col-sm-8 col-xs-12">
                <div class="select form-control">
                  <select @change="getFieldId" v-model="getData.farmFieldId">
                    <option>Please select</option>
                    <option v-for="field in $store.state.fields" :value="field.id">{{field.name}}</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
          <div class="col-lg-5 offset-lg-1">
            <div class="form-group row">
              <div class="col-lg-4 col-md-4 col-sm-4 col-xs-12">
                <label>Start Date</label>
              </div>
              <div class="col-lg-8 col-md-8 col-sm-8 col-xs-12">
                <datepicker @input="getStartTime" :value="getData.startTime"></datepicker>
              </div>
            </div>
            <div class="form-group row">
              <div class="col-lg-4 col-md-4 col-sm-4 col-xs-12">
                <label>End Date</label>
              </div>
              <div class="col-lg-8 col-md-8 col-sm-8 col-xs-12">
                <datepicker @input="getEndTime" :value="getData.endTime"></datepicker>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="card-footer" v-if="showRemoveBtn">
      <button @click="removeField" class="btn btn-sm btn-warning">remove</button>
    </div>
  </div>
</template>

<script>
  import Datepicker from 'vuejs-datepicker';
  import formatDate from '../../helpers/Formatter/format-date';

  export default {
    data() {
      return {
        getData: {
          activityId: '',
          farmFieldId: '',
          startTime: '',
          endTime: ''
        }
      };
    },
    components: {
      Datepicker
    },
    props: ['value'],
    computed: {
      indexInArray() {
        return this.$parent.model.activitySchedulers.indexOf(this.value);
      },
      showRemoveBtn() {
        return this.$parent.model.activitySchedulers.length > 1;
      },
      fieldId() {
        return this.$parent.model.activitySchedulers[this.indexInArray].id;
      },
      pageType() {
        return this.$route.params.url === 'new';
      }
    },
    methods: {
      getActivityId() {
        this.$emit('input', this.getData.activityId);
      },
      getFieldId() {
        this.$emit('input', this.getData.farmFieldId);
      },
      getStartTime(value) {
        this.getData.startTime = formatDate(value);
        this.$emit('input', this.getData);
      },
      getEndTime(value) {
        this.getData.endTime = formatDate(value);
        this.$emit('input', this.getData);
      },
      removeField() {
        if (this.fieldId) {
          this.$parent.model.fields4Delete = [];
          this.$parent.model.fields4Delete.push(this.fieldId);
        }
        if (this.indexInArray != -1) {
          this.$parent.model.activitySchedulers.splice(this.indexInArray, 1);
        }
      }
    }
  };
</script>



