<template>
  <div>
    <router-link :to="{ name: 'FarmsForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> Add Farm</router-link>
    <table class="table item-list">
      <tbody>
        <tr v-for="farm in farms">
          <td class="left"><router-link :to="{ name: 'FarmsItem', params: { url: 'edit', id: farm.id}}">{{farm.name}}</router-link></td>
          <td class="actions"><router-link class="btn btn-outline btn-sm" :to="{ name: 'FarmsItem', params: { url: 'edit', id: farm.id}}">edit</router-link></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        farms: {}
      };
    },
    mounted() {
      this.$store.dispatch('getFarms', this.$store.state).then(r => {
        this.farms = Object.assign({}, r.data);
      });
    }
  };
</script>



