<template>
  <div class="col-sm-12 col-md-12" v-if="status !== 'started' && status !=='completed'">
    <div class="alert" v-bind:class="status">
      <div class="alert-items">
        <div class="alert-item">
          <div class="alert-icon-wrapper">
            <clr-icon class="alert-icon" shape="exclamation-triangle"></clr-icon>
          </div>
          <span class="alert-text">You have <b>{{total}}</b> activity scheduler with status <b>{{status.toLowerCase()}}</b>!</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['status'],
    data() {
      return {
        total: ''
      };
    },
    mounted() {
      const sendData = {
        token: this.$store.state.token,
        satus: this.status
      };
      this.$store.dispatch('getDashboardStatistic', sendData).then(r => {
        this.total = r.total;
      });
    }
  };
</script>

<style>
  .alert.hasProblem {
    background: #feecb5;
    color: #565656;
    border: 1px solid #FFDC0B;
  }
  .alert.failed {
    background: #f5dbd9;
    color: #565656;
    border: 1px solid #ebafa6;
  }

  .alert.started {
    background: #e1f1f6;
    color: #565656;
    border: 1px solid #49afd9;
  }

  .alert.completed {
    background: #dff0d0;
    color: #565656;
    border: 1px solid #60b515;
  }

</style>



