import request from '../HttpRequest';
import sendToken from '../HttpRequest/send-token';
import { includes } from 'lodash';

function CalendarClass() {
  this.getEvents = function(data) {
    sendToken(data);
    return request.get(
      'v1/activities/schedulers?date_start=' +
        data.startDate +
        '&date_end=' +
        data.endDate
    );
  };
  this.getStatistic = function(data) {
    sendToken(data);
    return request.get(
      'v1/activities/schedulers?filter=and(in(' + data.satus + ',true))'
    );
  };
}

export default CalendarClass;



