<template>
  <div>
    <h2>{{ pageTitle }}</h2>
    <vue-form-generator :schema='schema' :model='model' :options='formOptions'></vue-form-generator>
  </div>
</template>

<script>
  import VueFormGenerator from 'vue-form-generator';
  import notification from '../../../helpers/Notification';
  import router from '../../../router/index';
  import required from '../../../helpers/Formatter/validator';

  export default {
    components: {
      'vue-form-generator': VueFormGenerator.component
    },
    data() {
      return {
        model: {
          firstName: '',
          lastName: '',
          loginName: '',
          password: '',
          email: '',
          roleIds: []
        },
        schema: {
          fields: [
            {
              type: 'input',
              inputType: 'text',
              label: 'First Name',
              model: 'firstName',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Last Name',
              model: 'lastName',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'text',
              label: 'Login',
              model: 'loginName',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'password',
              label: 'Password',
              model: 'password',
              required: true,
              validator: required
            },
            {
              type: 'input',
              inputType: 'email',
              label: 'Email',
              model: 'email',
              required: true,
              validator: required
            },
            {
              type: 'label',
              label: 'Roles'
            },
            {
              type: 'checklist',
              listBox: true,
              label: 'Select role',
              model: 'roleIds',
              required: true,
              validator: required,
              values: function() {
                return this.$store.state.roles;
              },
              checklistOptions: {
                value: 'id'
              }
            },
            {
              type: 'custom-submit',
              buttonText: 'Save',
              classes: 'btn-primary',
              onSubmit: formData => this.sendData(formData)
            },
            {
              type: 'back',
              buttonText: 'Back'
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      },
      pageTitle() {
        return this.pageType
          ? 'Add new user'
          : 'Edit user ' + this.$route.params.id;
      },
      queryType() {
        return this.pageType ? 'postUser' : 'putUser';
      },
      messageType() {
        return this.pageType
          ? 'User successfully added'
          : 'User successfully updated';
      },
      userData() {
        return {
          token: this.$store.state.token,
          id: this.$route.params.id
        };
      }
    },
    methods: {
      sendData: function(data) {
        data.token = this.$store.state.token;
        this.$store
          .dispatch(this.queryType, data)
          .then(() => {
            notification.success(this.messageType);
            router.push({ name: 'users', params: this.$route.params });
          })
          .catch(e => {
            const serverError = 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    },
    mounted() {
      if (!this.pageType) {
        this.$store.dispatch('getUser', this.userData).then(r => {
          this.model = Object.assign({}, r.data);
          this.model.roleIds = r.data.roles.map(function(x) {
            return x.id;
          });
        });
      }
      this.$store.dispatch('setRoles', this.$store.state);
    }
  };
</script>



