<template>
  <div>
    <router-link :to="{ name: 'ActivityForm', params: { url: 'new'}}" class="btn btn-primary"><clr-icon shape="add" size="20"></clr-icon> Add Activity</router-link>
    <table class="table item-list">
      <tbody>
        <tr v-for="activity in activities">
          <td class="left"><router-link :to="{ name: 'ActivityItem', params: { url: 'edit', id: activity.id}}">{{activity.name}}</router-link></td>
          <td class="actions"><router-link :to="{ name: 'ActivityItem', params: { url: 'edit', id: activity.id}}" class="btn btn-outline btn-sm">edit</router-link></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        activities: {}
      };
    },
    mounted() {
      this.$store.dispatch('getActivitys', this.$store.state).then(r => {
        this.activities = Object.assign({}, r.data);
      });
    }
  };
</script>



