// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import store from './store/index';
import router from './router';

import 'clarity-icons';
import 'clarity-icons/shapes/essential-shapes';
import VueFormGenerator from 'vue-form-generator';
import './helpers/vue-event-calendar/dist/style.css';
import vueEventCalendar from './helpers/vue-event-calendar';
import fieldCustomSelect from './helpers/CustomFields/fieldCustomSelect.vue';
import fieldBack from './helpers/CustomFields/fieldBack.vue';
import fieldDelete from './helpers/CustomFields/fieldDelete.vue';
import fieldCustomSubmit from './helpers/CustomFields/fieldCustomSubmit.vue';
import fieldGoogleMap from './helpers/CustomFields/fieldGoogleMap.vue';
import fieldSwitches from './helpers/CustomFields/fieldSwitches.vue';
import fieldRoleSwitches from './helpers/CustomFields/fieldRoleSwitches.vue';
import fieldDatePicker from './helpers/CustomFields/fieldDatePicker.vue';
import fieldRepeater from './helpers/CustomFields/fieldRepeater.vue';

import 'clarity-ui/clarity-ui.min.css';
import 'clarity-icons/clarity-icons.min.css';
import '../src/assets/main.css';
import VueTabs from 'vue-nav-tabs';

Vue.use(VueFormGenerator);
Vue.use(VueTabs);
Vue.use(vueEventCalendar, { locale: 'en', color: '#004a70' });

Vue.component('fieldCustomSelect', fieldCustomSelect);
Vue.component('fieldBack', fieldBack);
Vue.component('fieldDelete', fieldDelete);
Vue.component('fieldCustomSubmit', fieldCustomSubmit);
Vue.component('fieldGoogleMap', fieldGoogleMap);
Vue.component('fieldSwitches', fieldSwitches);
Vue.component('fieldRoleSwitches', fieldRoleSwitches);
Vue.component('fieldDatePicker', fieldDatePicker);
Vue.component('fieldRepeater', fieldRepeater);
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
});



