<template>
  <header class="header-5">
    <div class="branding">
      <div class="title">
        <clr-icon shape="grid-view" size="30"></clr-icon>
        <span>Agritex System</span>
      </div>
    </div>
    <div v-if="$store.state.token" class="header-actions">
      <div class="nav-text">{{ userName }} <clr-icon @click="logoutUser()" shape="logout" size="20"></clr-icon></div>
    </div>
  </header>
</template>

<script>
  import notification from '../helpers/Notification';
  import router from '../router/index';

  export default {
    computed: {
      userName() {
        return (
          this.$store.state.user.firstName + ' ' + this.$store.state.user.lastName
        );
      }
    },
    methods: {
      logoutUser() {
        this.$store.dispatch('logoutUser');
        router.push({ name: 'login' });
        notification.warn('You are Logout');
      }
    }
  };
</script>

<style>
  .branding span {
    position: relative;
    top:2px;
    left: -5px;
  }
  .header-actions .nav-text {
    position: relative;
    line-height: 60px;
    height: 60px;
    color: #fafafa;
    display: inline-block;
  }

  .header-actions .nav-text clr-icon {
    cursor: pointer;
  }
</style>



