<template>
  <div>
    <h2>{{pageTitle}}</h2>
    <vue-form-generator :schema='schema' :model='model' :options='formOptions'></vue-form-generator>
  </div>
</template>

<script>
  import VueFormGenerator from 'vue-form-generator';
  import notification from '../../../helpers/Notification';
  import router from '../../../router/index';
  import required from '../../../helpers/Formatter/validator';

  export default {
    components: {
      'vue-form-generator': VueFormGenerator.component
    },
    data() {
      return {
        model: {
          activityTypeId: '',
          name: ''
        },
        schema: {
          fields: [
            {
              type: 'input',
              inputType: 'text',
              label: 'Name',
              model: 'name',
              required: true,
              validator: required
            },
            {
              type: 'custom-select',
              label: 'Categories',
              required: true,
              validator: required,
              model: 'activityTypeId',
              values: function() {
                return this.$store.state.activityType;
              }
            },
            {
              type: 'custom-submit',
              buttonText: 'Save',
              classes: 'btn-primary',
              onSubmit: formData => this.sendData(formData)
            },
            {
              type: 'back',
              buttonText: 'Back'
            },
            {
              type: 'delete',
              entity: 'activities',
              buttonText: 'Delete'
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      },
      pageTitle() {
        return this.pageType
          ? 'Add activity'
          : 'Edit activity ' + this.$route.params.id;
      },
      queryType() {
        return this.pageType ? 'postActivity' : 'putActivity';
      },
      messageType() {
        return this.pageType
          ? 'Activity successfully added'
          : 'Activity successfully updated';
      },
      ActivityTypeData() {
        return {
          token: this.$store.state.token,
          id: this.$route.params.id
        };
      }
    },
    mounted() {
      if (!this.pageType) {
        this.$store.dispatch('getActivity', this.ActivityTypeData).then(r => {
          this.model = Object.assign({}, r.data);
        });
      }
      this.$store.dispatch('setActivityType', this.$store.state);
    },
    methods: {
      sendData: function(data) {
        data.token = this.$store.state.token;
        this.$store
          .dispatch(this.queryType, data)
          .then(() => {
            notification.success(this.messageType);
            router.back();
          })
          .catch(e => {
            const serverError = 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    }
  };
</script>

<style>
  .field-wrap .buttons {
    margin-top: 5px;
  }
</style>



