import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import db from '../helpers/Db';
import roleNameFormatter from '../helpers/Formatter/roles';
import debounce from 'lodash/fp/debounce';
import { pick, map, forEach } from 'lodash';

Vue.use(Vuex);

const state = {
  user: {},
  token: '',
  users: {},
  clusters: {},
  farms: {},
  farmsByClusterId: {},
  fields: {},
  roles: {},
  activitys: {},
  activityType: {},
  activitySchedulers: {},
  seasonsType: {},
  reports: {},
  status: {},
  spinner: false
};

const setLoaderOn = commit => {
  'use strict';
  commit('SET_PROCESSING_ON');
};

const setLoaderOff = debounce(300)(commit => {
  commit('SET_PROCESSING_OFF');
});

const PrepareData = function(x) {
  return {
    id: x.id,
    name: x.name
  };
};

const mutations = {
  SET_PROCESSING_ON(state) {
    state.spinner = true;
  },
  SET_PROCESSING_OFF(state) {
    state.spinner = false;
  },
  SET_USER(state, user) {
    state.user = user.data;
  },
  SET_USERS(state, users) {
    state.users = users.data.map(x => {
      return {
        id: x.id,
        name: x.firstName + ' ' + x.lastName
      };
    });
  },
  SET_TOKEN(state, token) {
    state.token = token;
  },
  SET_CLUSTERS(state, clusters) {
    state.clusters = clusters.data.map(x => PrepareData(x));
  },
  SET_FARMS_BY_CLUSTER(state, farms) {
    state.farmsByClusterId = farms.data.map(x => PrepareData(x));
  },
  SET_ROLES(state, roles) {
    state.roles = roles.data.map(x => {
      return {
        id: x.id,
        name: roleNameFormatter(x.name),
        value: x.name
      };
    });
  },
  SET_FARMS(state, farms) {
    state.farms = farms.data.map(x => PrepareData(x));
  },
  SET_FIELDS(state, fields) {
    state.fields = fields.data.map(x => PrepareData(x));
  },
  SET_ACTIVITY_TYPE(state, type) {
    state.activityType = type.data.map(x => PrepareData(x));
  },
  SET_ACTIVITYS(state, type) {
    state.activitys = type.data.map(x => PrepareData(x));
  },
  SET_ACTIVITYS_SCHEDULERS(state, type) {
    state.activitySchedulers = type.data.map(x => PrepareData(x));
  },
  SET_STATUS(state, data) {
    state.status = data;
  },
  SET_SEASONS_TYPE(state, type) {
    state.seasonsType = type.data.map(x => PrepareData(x));
  },
  SET_REPORTS(state, reports) {
    state.reports = reports.data;
  },
  LOGOUT_USER(state) {
    state.token = '';
    state.users = {};
    state.user = {};
    state.clusters = {};
    state.farms = {};
    state.fields = {};
    state.roles = {};
    state.activityType = {};
    state.activitys = {};
    state.seasonsType = {};
    state.spinner = false;
    state.farmsByClusterId = {};
    state.reports = {};
    state.activitySchedulers = {};
  }
};

const actions = {
  loginUser({ commit }, data) {
    return db.User.login(data).then(user => {
      commit('SET_USER', user);
    });
  },
  logoutUser({ commit }, data) {
    commit('LOGOUT_USER', data);
  },
  setUsers({ commit }, data) {
    return db.User.getUsers(data).then(users => {
      commit('SET_USERS', users);
    });
  },
  getUsers({ commit }, data) {
    setLoaderOn(commit);
    return db.User.getUsers(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  getUser({ commit }, data) {
    setLoaderOn(commit);
    return db.User.getUser(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  postUser({ commit }, data) {
    return db.User.postUser(data);
  },
  putUser({ commit }, data) {
    return db.User.putUser(data);
  },
  getRoles({ commit }, data) {
    setLoaderOn(commit);
    return db.User.getRoles(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  getRole({ commit }, data) {
    return db.User.getRole(data);
  },
  setToken({ commit }, data) {
    commit('SET_TOKEN', data);
  },
  setClusters({ commit }, data) {
    return db.Registration.getClusters(data).then(clusters => {
      commit('SET_CLUSTERS', clusters);
    });
  },
  setRoles({ commit }, data) {
    setLoaderOn(commit);
    return db.User.getRoles(data).then(roles => {
      commit('SET_ROLES', roles);
      setLoaderOff(commit);
    });
  },
  setFarms({ commit }, data) {
    return db.Registration.getFarms(data).then(farms => {
      commit('SET_FARMS', farms);
    });
  },
  setFields({ commit }, data) {
    return db.Registration.getFields(data).then(fields => {
      commit('SET_FIELDS', fields);
    });
  },
  setActivityType({ commit }, data) {
    return db.Activity.getActivityTypes(data).then(data => {
      commit('SET_ACTIVITY_TYPE', data);
    });
  },
  setActivitys({ commit }, data) {
    return db.Activity.getActivitys(data).then(activity => {
      commit('SET_ACTIVITYS', activity);
    });
  },
  setStatus({ commit }, data) {
    data = pick(data, ['completed', 'failed', 'hasProblem', 'started']);
    data = _.map(data, (v, key) => {
      let keyFormatter = key.toLowerCase();
      return {
        name: keyFormatter,
        value: key
      };
    });
    commit('SET_STATUS', data);
  },
  setActivitysSchedulers({ commit }, data) {
    return db.Activity.getActivitySchedulers(data).then(activity => {
      commit('SET_ACTIVITYS_SCHEDULERS', activity);
    });
  },
  setSeasonsType({ commit }, data) {
    return db.Seasons.getSeasonsTypes(data).then(data => {
      commit('SET_SEASONS_TYPE', data);
    });
  },
  setFarmByClusterId({ commit }, data) {
    return db.Seasons.getFarmByClusterId(data).then(data => {
      commit('SET_FARMS_BY_CLUSTER', data);
    });
  },
  checkTokenStatus({ commit }, data) {
    return db.User.getCurrentUser(data).catch(e => {
      setLoaderOff(commit);
      commit('LOGOUT_USER');
    });
  },
  getClusters({ commit }, data) {
    setLoaderOn(commit);
    return db.Registration.getClusters(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  postCluster({ commit }, data) {
    setLoaderOn(commit);
    return db.Registration.postCluster(data);
  },
  getCluster({ commit }, data) {
    setLoaderOn(commit);
    return db.Registration.getCluster(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  putCluster({ commit }, data) {
    return db.Registration.putCluster(data);
  },
  getFarms({ commit }, data) {
    setLoaderOn(commit);
    return db.Registration.getFarms(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  postFarm({ commit }, data) {
    return db.Registration.postFarm(data);
  },
  getFarm({ commit }, data) {
    setLoaderOn(commit);
    return db.Registration.getFarm(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  putFarm({ commit }, data) {
    return db.Registration.putFarm(data);
  },
  getFields({ commit }, data) {
    setLoaderOn(commit);
    return db.Registration.getFields(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  postField({ commit }, data) {
    return db.Registration.postField(data);
  },
  getField({ commit }, data) {
    setLoaderOn(commit);
    return db.Registration.getField(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  putField({ commit }, data) {
    return db.Registration.putField(data);
  },
  getReports({ commit }, data) {
    setLoaderOn(commit);
    return db.Reports.getReports(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  setReports({ commit }, data) {
    setLoaderOn(commit);
    return db.Reports.getReports(data).then(reports => {
      commit('SET_REPORTS', reports);
      setLoaderOff(commit);
    });
  },
  getReport({ commit }, data) {
    setLoaderOn(commit);
    return db.Reports.getReport(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  postReport({ commit }, data) {
    return db.Reports.postReport(data);
  },
  putReport({ commit }, data) {
    return db.Reports.putReport(data);
  },
  getSchedulers({ commit }, data) {
    setLoaderOn(commit);
    return db.Seasons.getSchedulers(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  getSeasonsTypes({ commit }, data) {
    setLoaderOn(commit);
    return db.Seasons.getSeasonsTypes(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  getSeasonsType({ commit }, data) {
    return db.Seasons.getSeasonsType(data);
  },
  postSeasonsType({ commit }, data) {
    return db.Seasons.postSeasonsType(data);
  },
  putSeasonsType({ commit }, data) {
    return db.Seasons.putSeasonsType(data);
  },
  postSeasonScheduler({ commit }, data) {
    return db.Seasons.postSeasonScheduler(data);
  },
  getSeasonScheduler({ commit }, data) {
    setLoaderOn(commit);
    return db.Seasons.getSeasonScheduler(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  putSeasonScheduler({ commit }, data) {
    return db.Seasons.putSeasonScheduler(data);
  },
  assignActivity({ commit }, data) {
    return db.Seasons.assignActivity(data);
  },
  getAssignActivity({ commit }, data) {
    return db.Seasons.getAssignActivity(data);
  },
  getActivitys({ commit }, data) {
    setLoaderOn(commit);
    return db.Activity.getActivitys(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  getActivity({ commit }, data) {
    return db.Activity.getActivity(data);
  },
  postActivity({ commit }, data) {
    return db.Activity.postActivity(data);
  },
  putActivity({ commit }, data) {
    return db.Activity.putActivity(data);
  },
  getActivityTypes({ commit }, data) {
    setLoaderOn(commit);
    return db.Activity.getActivityTypes(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  getActivityType({ commit }, data) {
    return db.Activity.getActivityType(data);
  },
  getActivitySchedulers({ commit }, data) {
    setLoaderOn(commit);
    return db.Activity.getActivitySchedulers(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  postActivityScheduler({ commit }, data) {
    return db.Activity.postActivityScheduler(data);
  },
  putActivityScheduler({ commit }, data) {
    return db.Activity.putActivityScheduler(data);
  },
  getActivityScheduler({ commit }, data) {
    setLoaderOn(commit);
    return db.Activity.getActivityScheduler(data).then(data => {
      setLoaderOff(commit);
      return data;
    });
  },
  putActivityScheduler({ commit }, data) {
    return db.Activity.putActivityScheduler(data);
  },
  postActivityType({ commit }, data) {
    return db.Activity.postActivityType(data);
  },
  putActivityType({ commit }, data) {
    return db.Activity.putActivityType(data);
  },
  deleteItem({ commit }, data) {
    if (confirm('Are you sure?')) {
      return db.Registration.deleteItem(data);
    }
  },
  getEvents({ commit }, data) {
    return db.Calendar.getEvents(data);
  },
  getDashboardStatistic({ commit }, data) {
    return db.Calendar.getStatistic(data);
  }
};

const store = new Vuex.Store({
  state,
  mutations,
  actions,
  plugins: [createPersistedState()]
});

export default store;



