<template>
  <div>
    <div class="form-group" v-for="role in $store.state.roles">

      <label v-bind:for="role.id">{{role.name}}</label>
      <div class="toggle-switch">
        <input type="checkbox" v-model="value" :autocomplete="schema.autocomplete" :disabled="disabled"  v-bind:id="role.id"/>
        <label v-bind:for="role.id"></label>
      </div>
    </div>
  </div>
</template>

<script>
  import { abstractField } from 'vue-form-generator';
  import roleNameFormatter from '../../helpers/Formatter/roles';

  export default {
    mixins: [abstractField],

    data: function() {
      return {
        formatValueToModel: '',
        dateValue: []
      };
    },
    computed: {
      roleModel() {
        return true;
      }
    },
    methods: {
      eventValue(val) {
        this.dateValue.push(parseInt(val.target.id));
        this.value = this.dateValue;
      }
    }
  };
</script>

<style>
  .vue-form-generator .field-checkbox input {
    margin-left: 12px;
  }
  .field-role-switches > label {
    display: none;
  }
</style>



