<template>
  <div>
    <component :is="schema.component" v-for='(data, index) in value' v-bind:key='index' v-model='value[index]' @input="onInput"></component>
    <button class="btn btn-outline" v-bind:class="schema.buttonSize" @click="AddNewField">{{ schema.buttonText }}</button>
  </div>
</template>

<script>
  import { abstractField } from 'vue-form-generator';
  import { isFunction } from 'lodash';
  import fieldReportField from './fieldReportField.vue';
  import fieldActivityField from './fieldActivityField.vue';

  export default {
    mixins: [abstractField],
    components: {
      fieldReportField,
      fieldActivityField
    },
    methods: {
      AddNewField() {
        this.value.push([]);
      },
      onInput() {
        if (isFunction(this.schema.inputCallback)) {
          return this.schema.inputCallback(this.value);
        }
      }
    }
  };
</script>


<style>
  .field-repeater > label {
    display: none;
  }
  .field-repeater .field-wrap {
    width: 80% !important;
  }
  .field-repeater .wrapper {
    width: 70%;
  }
</style>



