import request from '../HttpRequest';
import sendToken from '../HttpRequest/send-token';
import timeStampFormatter from '../../helpers/Formatter/timestamp';

const prepActivityScheduler = function(data) {
  data.endTime = timeStampFormatter(data.endTime);
  data.startTime = timeStampFormatter(data.startTime);
  data.completed = false;
  data.failed = false;
  data.hasProblem = false;
  data.started = false;
  data[data.status] = true;
  data.manageReports = {
    reportsAdd: data.reportIds
  };
  data.activitySchedulerTargets = [
    {
      farmField: {
        id: data.farmField
      }
    }
  ];
};

function ActivityClass() {
  this.getActivitys = function(data) {
    sendToken(data);
    return request.get('v1/activities');
  };
  this.getActivity = function(data) {
    sendToken(data);
    return request.get('v1/activities/' + data.id);
  };
  this.postActivity = function(data) {
    sendToken(data);
    return request.post('v1/activities', data);
  };
  this.putActivity = function(data) {
    sendToken(data);
    return request.put('v1/activities/' + data.id, data);
  };
  this.getActivityTypes = function(data) {
    sendToken(data);
    return request.get('v1/activities/types');
  };
  this.getActivityType = function(data) {
    sendToken(data);
    return request.get('v1/activities/types/' + data.id);
  };
  this.putActivityType = function(data) {
    sendToken(data);
    return request.put('v1/activities/types/' + data.id, data);
  };
  this.postActivityType = function(data) {
    sendToken(data);
    return request.post('v1/activities/types', data);
  };
  this.postActivityScheduler = function(data) {
    sendToken(data);
    prepActivityScheduler(data);
    return request.post('v1/activities/schedulers', data);
  };
  this.putActivityScheduler = function(data) {
    sendToken(data);
    prepActivityScheduler(data);
    return request.put('v1/activities/schedulers/' + data.id, data);
  };
  this.getActivitySchedulers = function(data) {
    sendToken(data);
    return request.get('v1/activities/schedulers');
  };
  this.getActivityScheduler = function(data) {
    sendToken(data);
    return request.get('v1/activities/schedulers/' + data.id);
  };
}

export default ActivityClass;



