<template>
  <div class="form-group field-input">
    <clr-icon v-if="showRemoveBtn" @click="removeField" class="is-danger" shape="close"></clr-icon>
    <label>Name</label>
    <div class="wrapper">
      <input type="text" class="form-control" :value="value.name" required="required" @input="getValue($event.target.value)">
    </div>
  </div>
</template>

<script>

  export default {
    props: ['value'],
    computed: {
      indexInArray() {
        return this.$parent.model.reportFields.indexOf(this.value);
      },
      showRemoveBtn() {
        return this.$parent.model.reportFields.length > 1;
      },
      fieldId() {
        return this.$parent.model.reportFields[this.indexInArray].id;
      }
    },
    methods: {
      getValue(value) {
        value = {
          name: value,
          fieldType: 'string'
        };
        this.$emit('input', value);
      },
      removeField() {
        if (this.fieldId) {
          this.$parent.model.fields4Delete = [];
          this.$parent.model.fields4Delete.push(this.fieldId);
        }
        if (this.indexInArray != -1) {
          this.$parent.model.reportFields.splice(this.indexInArray, 1);
        }
      }
    }
  };
</script>

<style>
  clr-icon[shape="close"] {
    position: relative;
    top:4px;
    cursor: pointer;
  }
</style>



