<template>
  <div>
    <input class="btn" :class='schema.classes' type='submit' :value='schema.buttonText' @click='click' :name='schema.inputName' :disabled='isBtnDisabled'/>
  </div>
</template>

<script>
  import { abstractField } from 'vue-form-generator';
  import { isFunction } from 'lodash';

  export default {
    mixins: [abstractField],

    methods: {
      click() {
        if (this.schema.validateBeforeSubmit === true) {
          if (!this.$parent.validate()) {
            return;
          }
        }
        if (isFunction(this.schema.onSubmit)) {
          this.schema.onSubmit(this.model, this.schema);
        }
      }
    },
    computed: {
      isBtnDisabled() {
        return this.$parent.validate() ? false : true;
      }
    }
  };
</script>



