function atomic(root) {
  'use strict';
  let exports = {};

  const config = {
    defaultContentType: 'application/json',
    contentType: 'application/json',
    requestedWith: 'XMLHttpRequest',
    sendToken: '',
    baseUrl: root || ''
  };

  const parse = function(req) {
    let result;
    try {
      result = JSON.parse(req.responseText);
    } catch (e) {
      result = req.responseText;
    }
    return result;
  };

  const xhr = function(type, url, data) {
    return new Promise((resolve, reject) => {
      const XHR = window.XMLHttpRequest;
      const request = new XHR('MSXML2.XMLHTTP.3.0');

      request.withCredentials = true;
      request.open(type, config.baseUrl + '' + url, true);
      request.setRequestHeader('Content-Type', config.contentType);
      request.setRequestHeader('X-Requested-With', config.requestedWith);
      if (config.sendToken) {
        request.setRequestHeader('X-Authorization', config.sendToken);
      }

      request.onreadystatechange = function() {
        if (request.readyState === 4) {
          const req = parse(request);
          if (request.status >= 200 && request.status < 300) {
            resolve(req);
          } else {
            reject(request);
          }
        }
      };

      const params =
        typeof data !== 'string' &&
        (type === 'PUT' || type === 'POST' || type === 'DELETE')
          ? JSON.stringify(data)
          : data;
      request.send(params);
    });
  };

  exports.get = function(src) {
    return xhr('GET', src);
  };

  exports.put = function(url, data) {
    return xhr('PUT', url, data);
  };

  exports.post = function(url, data) {
    return xhr('POST', url, data);
  };

  exports.delete = function(url, data) {
    return xhr('DELETE', url, data);
  };

  exports.setContentType = function(value) {
    config.contentType = value;
  };

  exports.sendToken = function(value) {
    config.sendToken = 'Bearer ' + value;
  };

  exports.setDefaultContentType = function() {
    config.contentType = config.defaultContentType;
  };

  exports.setBaseUrl = function(value) {
    config.baseUrl = value;
  };
  return exports;
}

export default atomic;



