<template>
  <div>
    <h2>{{pageTitle}}</h2>
    <vue-form-generator :schema='schema' :model='model' :options='formOptions'></vue-form-generator>
  </div>
</template>

<script>
  import VueFormGenerator from 'vue-form-generator';
  import required from '../../../helpers/Formatter/validator';

  const RoleName = function(data) {
    data.map(function(x) {
      return x;
    });
  };

  export default {
    components: {
      'vue-form-generator': VueFormGenerator.component
    },
    data() {
      return {
        model: {
          name: '',
          description: ''
        },
        schema: {
          fields: [
            {
              type: 'custom-select',
              label: 'Name',
              required: true,
              model: 'name',
              values: function() {
                return this.$store.state.roles.map(function(x) {
                  return {
                    name: x.name,
                    id: x.value
                  };
                });
              }
            },
            {
              type: 'textArea',
              inputType: 'text',
              label: 'Description',
              model: 'description',
              rows: 4
            },
            {
              type: 'custom-submit',
              buttonText: 'Save',
              classes: 'btn-primary',
              onSubmit: function(e) {
                console.log(e);
              }
            },
            {
              type: 'back',
              buttonText: 'Back'
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      },
      pageTitle() {
        return this.pageType
          ? 'Add new role'
          : 'Edit role ' + this.$route.params.id;
      },
      queryType() {
        return this.pageType ? 'postRole' : 'putRole';
      },
      messageType() {
        return this.pageType
          ? 'Role successfully added'
          : 'Role successfully updated';
      },
      roleData() {
        return {
          token: this.$store.state.token,
          id: this.$route.params.id
        };
      }
    },
    mounted() {
      if (!this.pageType) {
        this.$store.dispatch('getRole', this.roleData).then(r => {
          this.model = Object.assign({}, r.data);
        });
        this.$store.dispatch('setRoles', this.$store.state);
      }
    }
  };
</script>



