<template>
  <span class='modal-component'>
    <button @click='openModal()' class='btn btn-sm btn-danger-outline'>Assign Activity</button>
    <span v-if='modal'>
      <div class='modal'>
        <div class='modal-dialog' role='dialog' aria-hidden='true'>
          <div class='modal-content'>
            <div class='modal-header'>
              <button aria-label='Close' class='close' type='button' @click='modal = !modal'>
                <clr-icon aria-hidden='true' shape='close'></clr-icon>
              </button>
            </div>
            <div class='modal-body'>
              <h3 class='modal-title'>Activities Schedulers</h3>
              <vue-form-generator :schema='schema' :model='model' :options='formOptions'></vue-form-generator>
            </div>
          </div>
        </div>
      </div>
      <div class='modal-backdrop' aria-hidden='true'></div>
    </span>
  </span>
</template>

<script>
  import VueFormGenerator from 'vue-form-generator';
  import router from '../../../router/index';
  import notification from '../../../helpers/Notification';
  import required from '../../../helpers/Formatter/validator';

  export default {
    components: {
      'vue-form-generator': VueFormGenerator.component
    },
    props: ['shedulaerId'],
    data() {
      return {
        modal: false,
        model: {
          activitySchedulersId: []
        },
        schema: {
          fields: [
            {
              type: 'checklist',
              listBox: true,
              label: '',
              model: 'activitySchedulersId',
              required: true,
              validator: required,
              values: function() {
                return this.$store.state.activitySchedulers;
              },
              checklistOptions: {
                value: 'id'
              }
            },
            {
              type: 'custom-submit',
              buttonText: 'Assign',
              onSubmit: formData => this.sendData(formData)
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    computed: {
      messageType() {
        return this.pageType
          ? 'Activity successfully assign'
          : 'Activity successfully updated';
      },
      schedulerData() {
        return {
          token: this.$store.state.token,
          id: this.shedulaerId
        };
      }
    },
    methods: {
      openModal() {
        this.modal = !this.modal;
        this.$store.dispatch('setActivitysSchedulers', this.$store.state);
        this.$store.dispatch('getAssignActivity', this.schedulerData).then(x => {
          if (x.data.length > 0) {
            x.data = x.data.map(x => {
              this.model.activitySchedulersId.push(x.id);
            });
          }
        });
      },
      sendData: function(data) {
        data.token = this.$store.state.token;
        data.id = this.shedulaerId;
        this.$store
          .dispatch('assignActivity', data)
          .then(() => {
            notification.success(this.messageType);
            this.modal = !this.modal;
          })
          .catch(e => {
            const serverError = 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    }
  };
</script>

<style>
  .modal {
    text-align: left;
  }
  .modal .field-checklist > label {
    display: none;
  }
</style>



