const roleTitles = {
  ROLE_DIRECTOR: 'Director',
  ROLE_FIELD: 'Field Officer',
  ROLE_FARM: 'Farm Manager',
  ROLE_CLUSTER: 'Cluster Manager',
  ROLE_ADMIN: 'Administrator'
};

const roleNameFormatter = function(name) {
  return roleTitles[name] || name;
};

export default roleNameFormatter;



