<template>
  <div>
    <h2>{{pageTitle}}</h2>
    <vue-form-generator :schema='schema' :model='model' :options='formOptions'></vue-form-generator>
  </div>
</template>

<script>
  import VueFormGenerator from 'vue-form-generator';
  import notification from '../../../helpers/Notification';
  import router from '../../../router/index';
  import required from '../../../helpers/Formatter/validator';

  export default {
    components: {
      'vue-form-generator': VueFormGenerator.component
    },
    data() {
      return {
        model: {
          name: '',
          reportFields: []
        },
        schema: {
          fields: [
            {
              type: 'input',
              inputType: 'text',
              label: 'Name',
              model: 'name',
              required: true,
              validator: required
            },
            {
              type: 'label',
              label: 'Report Fields'
            },
            {
              type: 'repeater',
              model: 'reportFields',
              component: 'field-report-field',
              buttonText: 'Add new field',
              buttonSize: 'btn-sm',
              required: true,
              validator: required
            },
            {
              type: 'custom-submit',
              buttonText: 'Save',
              classes: 'btn-primary',
              onSubmit: formData => this.sendData(formData)
            },
            {
              type: 'back',
              buttonText: 'Back'
            },
            {
              type: 'delete',
              entity: 'reports',
              buttonText: 'Delete'
            }
          ]
        },
        formOptions: {
          validateAfterLoad: true,
          validateAfterChanged: true
        }
      };
    },
    computed: {
      pageType() {
        return this.$route.params.url === 'new';
      },
      pageTitle() {
        return this.pageType
          ? 'Add new report'
          : 'Edit report ' + this.$route.params.id;
      },
      queryType() {
        return this.pageType ? 'postReport' : 'putReport';
      },
      messageType() {
        return this.pageType
          ? 'Report successfully added'
          : 'Report successfully updated';
      },
      reportData() {
        return {
          token: this.$store.state.token,
          id: this.$route.params.id
        };
      }
    },
    mounted() {
      this.model.reportFields.push('');
      if (!this.pageType) {
        this.$store.dispatch('getReport', this.reportData).then(r => {
          this.model = Object.assign({}, r.data);
        });
      }
    },
    methods: {
      sendData: function(data) {
        data.token = this.$store.state.token;
        this.$store
          .dispatch(this.queryType, data)
          .then(() => {
            notification.success(this.messageType);
            router.back();
          })
          .catch(e => {
            const serverError = 'Undefined error on server.';
            notification.error(serverError);
          });
      }
    }
  };
</script>



